import { Component, OnInit, Output, Input, OnChanges, EventEmitter } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import * as _ from 'lodash';

import { ProjectHttpService } from '../../../http/project-main-manage.service';
import { SupplyChainPlatformAdd, supplyChainPlatformList, supplyChainPlatformDetail, supplyChainPlatformModify, supplyChainPlatformDelete } from '../bean/three-platform.bean';
import { Router, ActivatedRoute } from '@angular/router';
import { checkEnAndNum } from 'app/@uisftech/common/service/validator';
@Component({
    selector: "three-platform-add-modify",
    templateUrl: './three-platform-add-modify.component.html',
    styleUrls: ['./three-platform-add-modify.component.scss'],
})

export class ThreePlatformAddModify implements OnInit {
    modifyplatformIdentify: any;
    type: any;
    msgs: any;
    pageSize: number;
    pageNum: number;
    first: any = 0;
    total: any;


    //数据bean
    detailBean: SupplyChainPlatformAdd = new SupplyChainPlatformAdd();

    //码值
    code: any;
    coopMode: any[];
    signFeedback: any[];
    projectApproveNo: any[];
    projectApproveYes: any[];
    beforeCreditFeedback: any[];
    creditFeedback: any[];
    shipmentFlag: any[];
    payFeedback: any[];
    repayFeedback: any[];

    constructor(
        public httpService: ProjectHttpService,
        public commfunc: Commfunc,
        public activatedRoute: ActivatedRoute,
        public router: Router,
    ) {

    }
    ngOnInit() {
        this.codeValues();

        this.activatedRoute.queryParams.subscribe(queryParams => {
            console.log(queryParams)
            this.type = queryParams.type;//1为新增2为修改
            if (this.type == '2') {
                this.modifyplatformIdentify = queryParams.platformIdentify;
            }

        })
        //列表项取值
        this.coopMode = this.code['coopMode'];//线上线下标志
        this.signFeedback = this.code['signFeedback'];// 签约是否反馈
        this.projectApproveNo = this.code['projectApproveNo'];// 立项否决反馈
        this.projectApproveYes = this.code['projectApproveYes'];// 立项通过反馈
        this.beforeCreditFeedback = this.code['beforeCreditFeedback'];//预授信反馈
        this.creditFeedback = this.code['creditFeedback'];// 授信反馈
        this.shipmentFlag = this.code['shipmentFlag'];//发货控制
        this.payFeedback = this.code['payFeedback']; //代付反馈
        this.repayFeedback = this.code['repayFeedback'];//还款反馈
        if (this.type == '2') {
            this.QueryModify();
        }
    }
    // 查询数据·修改
    QueryModify() {
        let param = {
            platformIdentify: this.modifyplatformIdentify,
        }
        this.httpService.supplyChainPlatformDetail(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.detailBean.platformIdentify = data.platformIdentify;
                this.detailBean.platformName = data.platformName;
                this.detailBean.platformDesc = data.platformDesc;
                this.detailBean.coopMode = data.platformCtrl.coopMode;
                this.detailBean.signFeedback = data.platformCtrl.signFeedback;
                this.detailBean.projectApproveNo = data.platformCtrl.projectApproveNo;
                this.detailBean.projectApproveYes = data.platformCtrl.projectApproveYes;
                this.detailBean.beforeCreditFeedback = data.platformCtrl.beforeCreditFeedback;
                this.detailBean.creditFeedback = data.platformCtrl.creditFeedback;
                this.detailBean.shipmentFlag = data.platformCtrl.shipmentFlag;
                this.detailBean.payFeedback = data.platformCtrl.payFeedback;
                this.detailBean.repayFeedback = data.platformCtrl.repayFeedback;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
    }
    // 提交
    submit() {
        let param;
        if (this.detailBean.coopMode != '2') {
            param = {
                platformIdentifyBefore: this.modifyplatformIdentify,
                platformIdentify: this.detailBean.platformIdentify,
                platformName: this.detailBean.platformName,
                platformDesc: this.detailBean.platformDesc,
                operateType: '2',
                platformCtrl: {
                    coopMode: this.detailBean.coopMode,
                    signFeedback: this.detailBean.signFeedback,
                    projectApproveNo: this.detailBean.projectApproveNo,
                    projectApproveYes: this.detailBean.projectApproveYes,
                    beforeCreditFeedback: this.detailBean.beforeCreditFeedback,
                    creditFeedback: this.detailBean.creditFeedback,
                    shipmentFlag: this.detailBean.shipmentFlag,
                    payFeedback: this.detailBean.payFeedback,
                    repayFeedback: this.detailBean.repayFeedback,
                }
            }
        } else {
            param = {
                operateType: '2',
                platformIdentifyBefore: this.modifyplatformIdentify,
                platformIdentify: this.detailBean.platformIdentify,
                platformName: this.detailBean.platformName,
                platformDesc: this.detailBean.platformDesc,
                platformCtrl: {
                    coopMode: this.detailBean.coopMode,
                }
            }
        }
        // 页面修改状态Type=2
        if (this.type == '2') {
            this.httpService.supplyChainPlatformModify(param).subscribe(data => {
                if (data.returnCode.code == "success") {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                    this.goback();
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
            })
        }
        // 页面新增状态Type=1
        else {
            this.httpService.supplyChainPlatformAdd(param).subscribe(data => {
                if (data.returnCode.code == "success") {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                    this.goback();
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
            })
        }

    }
    // 保存
    save() {
        let param;
        if (this.detailBean.coopMode != '2') {
            param = {
                platformIdentifyBefore: this.modifyplatformIdentify,
                platformIdentify: this.detailBean.platformIdentify,
                platformName: this.detailBean.platformName,
                platformDesc: this.detailBean.platformDesc,
                operateType: '1',
                platformCtrl: {
                    coopMode: this.detailBean.coopMode,
                    signFeedback: this.detailBean.signFeedback,
                    projectApproveNo: this.detailBean.projectApproveNo,
                    projectApproveYes: this.detailBean.projectApproveYes,
                    beforeCreditFeedback: this.detailBean.beforeCreditFeedback,
                    creditFeedback: this.detailBean.creditFeedback,
                    shipmentFlag: this.detailBean.shipmentFlag,
                    payFeedback: this.detailBean.payFeedback,
                    repayFeedback: this.detailBean.repayFeedback,
                }
            }
        } else {
            param = {
                operateType: '1',
                platformIdentifyBefore: this.modifyplatformIdentify,
                platformIdentify: this.detailBean.platformIdentify,
                platformName: this.detailBean.platformName,
                platformDesc: this.detailBean.platformDesc,
                platformCtrl: {
                    coopMode: this.detailBean.coopMode,
                }
            }
        }
        // 页面修改状态Type=2
        if (this.type == '2') {
            this.httpService.supplyChainPlatformModify(param).subscribe(data => {
                if (data.returnCode.code == "success") {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                    this.goback();
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
            })
        }
        // 页面新增状态Type=1
        else {
            this.httpService.supplyChainPlatformAdd(param).subscribe(data => {
                if (data.returnCode.code == "success") {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                    this.goback();
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
            })
        }

    }
    goback() {
        // window.history.go(-1);
        this.router.navigate(['/pages/tzb/custom-center/project-main-manage/three-platform'], { queryParams: { index: 0 } })
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    onCheckEnAndNum(event) {
        if (event.target.value != '') {
            if (checkEnAndNum(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '当前输入项只能输数字或字母' }];
                event.target.value = '';
            }
        }
    }
}