import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { Router } from '@angular/router';

import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';
import { LoansTableBean, LoansTableDetailBean } from '../bean/contracts-loans.bean';
import { checkEnAndNum, checkNumber } from 'app/@uisftech/common/service/validator';


@Component({
    selector: "loans-table",
    templateUrl: './loans-table.component.html',
    styleUrls: ['./loans-table.component.scss'],
})

export class LoansTable implements OnInit {
    code: any;
    first: any = 1;
    total: any;
    dataTable: any[];
    msgs: any;
    ch: any = CALENDAR_CN;
    likeFlag: any = [];
    bean: LoansTableBean = new LoansTableBean();
    detailBean: LoansTableDetailBean = new LoansTableDetailBean();
    tableHead: any[] = [{
        label: '协议编号',
        value: 'projectId',
        wth: '150px'
    }, {
        label: '核心企业客户号',
        value: 'idOfCoreByBank',
        wth: '150px'
    }, {
        label: '核心企业名称',
        value: 'custName',
        wth: '150px'
    }, {
        label: '核心企业ID号',
        value: 'platformCustId',
        wth: '150px'
    }, {
        label: '客户号',
        value: 'loancustId',
        wth: '150px'
    }, {
        label: '客户名称',
        value: 'loanCustName',
        wth: '150px'
    }, {
        label: '借据号',
        value: 'receiptLoanId',
        wth: '150px'
    }, {
        label: '合同编号',
        value: 'contractNumber',
        wth: '150px'
    }];
    tableHead1: any[] = [{
        label: '起贷日期',
        value: 'loanStartDate',
        wth: '150px'
    }, {
        label: '止贷日期',
        value: 'loanDueDate',
        wth: '150px'
    }];
    beginLoanStartDate: Date = null;
    endLoanStartDate: Date = null;
    beginLoanDueDate: Date = null;
    endLoanDueDate: Date = null;
    loandis: boolean = false;
    addValue: any = [];
    constructor(
        public router: Router,
        public http: ProjectHttpService,
        public commfunc: Commfunc,
    ) {
        this.codeValues();
    }

    ngOnInit() {
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.queryList()
    }

    query() {
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.queryList()
    }

    queryList() {
        this.msgs = [];
        //增加模糊查询
        // if (this.likeFlag > 0) {
        this.bean.likeFlag = 1;
        // } else {
        //     this.bean.likeFlag = 0;
        // }
        if (this.beginLoanStartDate && typeof this.beginLoanStartDate != 'string') {
            this.bean.beginLoanStartDate = this.commfunc.transDateN(this.beginLoanStartDate);
        }
        if (this.endLoanStartDate && typeof this.endLoanStartDate != 'string') {
            this.bean.endLoanStartDate = this.commfunc.transDateN(this.endLoanStartDate);
        }
        if (this.beginLoanDueDate && typeof this.beginLoanDueDate != 'string') {
            this.bean.beginLoanDueDate = this.commfunc.transDateN(this.beginLoanDueDate);
        }
        if (this.endLoanDueDate && typeof this.endLoanDueDate != 'string') {
            this.bean.endLoanDueDate = this.commfunc.transDateN(this.endLoanDueDate);
        }
        this.http.supplyChainLoanQueryList(this.bean).subscribe(data => {
            if (data['returnCode']['code'] == 'success') {
                this.dataTable = data['returnList'];
                this.total = data['totalNum'];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            }
            else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }

    clear() {
        this.bean = new LoansTableBean();
        this.beginLoanStartDate = null;
        this.endLoanStartDate = null;
        this.beginLoanDueDate = null;
        this.endLoanDueDate = null;
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.queryList()
    }
    // 详情
    detail(e) {
        this.loandis = true;
        this.addValue = e
    }

    paginate(event) {
        // 分页
        this.bean.pageSize = event.rows;
        let currentPage = event.page + 1;
        this.bean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.bean.pageSize;
        this.queryList();
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret
    }
    // 非数校验
    onCheck(event) {
        this.msgs = [];
        if (event.target.value != '') {
            if (checkNumber(null, event.target.value)) {
                this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入正确的数字查询值' });
                event.target.value = '';
            }
        }
    }
}