import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { TZB_HTTP_XIN } from 'app/@uisftech/common/service/http.constant';
import { Router } from '@angular/router';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { OrderInfoTabBean } from './bean/orderInfoTab.bean';
import { SupplyChainHttpService } from '../http/supply-chain.http.service';
import { checkNumber,decimalNumTwo } from '../../../../../../../@uisftech/common/service/validator';
import { CusViewHttpService } from '../../../../../custom/http/custom-view/custom-view.http.service';
@Component({
    selector: 'order-info-table',
    templateUrl: './order-info-table.component.html',
    styleUrls: ['./order-info-table.component.scss'],
    providers: [Commfunc, SupplyChainHttpService, CusViewHttpService]
})
/**订单信息表 */
export class OrderInfoTable implements OnInit {
    orgTreeDisplay = false;
    msgs: Message[] = []; //提示信息
    orderInfoTabBean: OrderInfoTabBean = new OrderInfoTabBean()
    dataList: any = [];
    code: any;
    SignStatus: any = [];//签约状态
    ch: any = CALENDAR_CN;
    CorporationCode: any = [];
    first: number = 0;//分页控制
    totalNum: number;
    signDate: Date = null;
    recommendDate: Date = null;
    OrderStatus: any = [];//订单类型
    detailPage: boolean = false;
    showFlge: string;
    detailHeader: string;
    inValue: any = {};
    startTime: Date = null;
    endTime: Date = null;
    likeFlag: any = [];
    constructor(
        public projectHttpService: SupplyChainHttpService,
        public commfunc: Commfunc,
        public httpService: CusViewHttpService,

    ) {
        this.codeValues() //调用方法，获取全部码值
        this.OrderStatus = this.code['ORDERSTATUS']; //证件类型

    }

    ngOnInit() {
        this.orderInfoTabBean.pageNum = 1;
        this.orderInfoTabBean.pageSize = 10;
        this.first = 0;
        this.queryClick();
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //查询按钮
    queryClick() {
        this.orderInfoTabBean.pageNum = 1;
        this.orderInfoTabBean.pageSize = 10;
        this.first = 0;
        this.query();
    }
    //查询
    query() {
        //增加模糊查询
        // if (this.likeFlag > 0) {
            this.orderInfoTabBean.likeFlag = 1;
        // } else {
        //     this.orderInfoTabBean.likeFlag = 0;
        // }
        this.orderInfoTabBean.endTime = this.commfunc.transDate(this.endTime);
        this.orderInfoTabBean.startTime = this.commfunc.transDate(this.startTime);
        this.projectHttpService.supplyChainWebOrderList(this.orderInfoTabBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.totalNum = data.totalNum;
                this.dataList = data.infoList;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: '查询成功' });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
    }
    perTreeShow(){
        this.orgTreeDisplay = true;
    }
    onHideClose() {
        this.orgTreeDisplay = false;
      }
      treeCall(param) {
        this.orgTreeDisplay = false;
        this.orderInfoTabBean.branchName = param.orgName;
        this.orderInfoTabBean.branchId = param.orgId;
      }
    //重置
    resetClick() {
        this.startTime = null;
        this.endTime = null;
        this.orderInfoTabBean = new OrderInfoTabBean();
        this.querySecleReset();
    }
    //页码重置
    querySecleReset() {
        this.orderInfoTabBean.pageNum = 1;
        this.orderInfoTabBean.pageSize = 10;
        this.first = 0;
        this.query();
    }

    paginate　(event) {
        //每页显示的条数
        this.orderInfoTabBean.pageSize = Number(event.rows);
        //当前页
        let currentPage = event.page + 1;
        this.orderInfoTabBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.orderInfoTabBean.pageSize;
        this.query();
    }
    //开始日期
    showDataStart() {
        if (this.startTime && this.endTime) {
            if (this.startTime > this.endTime) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '下单起始日期不能大于下单结束始日期' });
                setTimeout(() => {
                    this.startTime = null;
                })
            }
        }
    }
    //结束日期
    showDataEnd() {
        if (this.startTime && this.endTime) {
            if (this.startTime > this.endTime) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '下单起始日期不能大于下单结束始日期' });
                setTimeout(() => {
                    this.endTime = null;
                })
            }
        }
    }
    //手动输入日期时，失去焦点时进行判断
    onchange1() {
        this.showDataStart()
    }
    onchange2() {
        this.showDataEnd()
    }

    //校验客户号只能输入数字
    onKeyupNumber(event, item) {
        // if (checkNumber(null, event.target.value)) {
        //     this.msgs = [{ severity: 'error', summary: '提示', detail: '客户号错误!' }];
        //     event.target.value = '';
        //     return
        // }
        if (item && item != '') {
            let param = {
                custNo: item
            }
            this.httpService.selectCustBriefInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.orderInfoTabBean.custName = data.custName;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        }
    }
    onKeyupNumId(event, num) {
        // if (num == '1') {
        //     if (checkNumber(null, event.target.value)) {
        //         this.msgs = [{ severity: 'error', summary: '提示', detail: '核心企业客户号错误!' }];
        //         event.target.value = '';
        //         return
        //     }
        // } else if (num == '2') {
        //     if (checkNumber(null, event.target.value)) {
        //         this.msgs = [{ severity: 'error', summary: '提示', detail: '核心企业ID号错误!' }];
        //         event.target.value = '';
        //         return
        //     }
        // } else if (num == '3') {
        //     if (checkNumber(null, event.target.value)) {
        //         this.msgs = [{ severity: 'error', summary: '提示', detail: '订单号错误!' }];
        //         event.target.value = '';
        //         return
        //     }
        // }

    }
    //查看详情
    toDetail(data) {
        this.showFlge = '1';
        this.detailHeader = '详情'
        this.detailPage = true;
        this.inValue = data;
    }
    //订单金额校验
    oderAmount(event) {
        if (decimalNumTwo(null, event.target.value)) {
            this.msgs = [{ severity: 'error', summary: '提示', detail: '输入正确的金额!' }];
            event.target.value = '';
            return
        }
    }
    //校验订单金额区间
    oderBlur(num) {
        if (num == '1') {
            if (parseInt(this.orderInfoTabBean.orderLessAmount) && parseInt(this.orderInfoTabBean.orderLargeAmount)) {
                if (parseInt(this.orderInfoTabBean.orderLessAmount) > parseInt(this.orderInfoTabBean.orderLargeAmount)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入正确的格式' });
                    setTimeout(() => {
                        this.orderInfoTabBean.orderLessAmount = "";
                    })
                }
            }
           
        }
        if (num == '2') {
            if (parseInt(this.orderInfoTabBean.orderLessAmount) && parseInt(this.orderInfoTabBean.orderLargeAmount)) {
                if (parseInt(this.orderInfoTabBean.orderLessAmount) > parseInt(this.orderInfoTabBean.orderLargeAmount)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入正确的格式' });
                    setTimeout(() => {
                        this.orderInfoTabBean.orderLargeAmount = "";
                    })
                }
            }
        }
    }
}
