import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message, ConfirmationService, ConfirmDialogModule } from 'primeng/primeng';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { Router } from '@angular/router';
import * as _ from 'lodash';
import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';
import { SupplyChainPlatformAdd, supplyChainPlatformList, supplyChainPlatformDetail, supplyChainPlatformModify, supplyChainPlatformDelete } from 'app/pages/tzb/customCenter/project-main-manage/task-center/bean/task-report.bean';
import { ActivatedRoute } from '@angular/router';



@Component({
    selector: "month-report-word",
    templateUrl: './priorities-report-word.component.html',
    styleUrls: ['./priorities-report-word.component.scss'],
    providers: [Commfunc, ConfirmationService]
})

export class PrioritiesReportWord implements OnInit {
    ch: any = CALENDAR_CN;
    code: any;
    first: any = 1;
    total: any;
    dataTable: any = [];
    msgs: any;
    bean: supplyChainPlatformList = new supplyChainPlatformList();
    deleteBean: supplyChainPlatformDelete = new supplyChainPlatformDelete();
    reportCode: string;
    operationTypes = [
        { value: "01", label: "部门报表" },
        { value: "02", label: "支行报表" },
        { value: "03", label: "分行报表" },
        { value: "04", label: "集团报表" }
    ];


    constructor(
        public router: Router,
        public http: ProjectHttpService,
        public commfunc: Commfunc,
        public activatedRouter: ActivatedRoute,
        public confirmationService: ConfirmationService,
    ) {
        this.codeValues();
        activatedRouter.queryParams.subscribe(queryParams => {
            this.bean['reportId'] = queryParams.partyOrGroupNo;
            this.bean['reportName'] = queryParams.partyOrGroupName;
            this.bean['createDate'] = queryParams.createDate;

        });
    }

    ngOnInit() {
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.TimeToDate();
        this.queryList();
    }

    queryList() {
        this.msgs = [];
        this.http.queryTaskTotalList(this.bean).subscribe(data => {
            if (data['returnCode']['code'] == 'success') {
                let dataList = data['taskTotalList'];
                console.log(dataList);
                // "TOTAL"
                let newDataList = [];
                dataList.forEach(element => {
                    let legacy = [];
                    element.forEach((data, i) => {
                        if (data['groupId'] == 'TOTAL') {
                            legacy = element.splice(i, 1);
                        }
                    });
                    element.push(legacy[0]);
                });
                this.dataTable = dataList;
            }
            else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }
    // 详情
    detail(e) {
        this.router.navigate(['/pages/tzb/custom-center/project-main-manage/three-platform-details'], { queryParams: { platformIdentify: e['platformIdentify'] } })
    }

    // 获取当月日期
    TimeToDate() {
        let date = this.bean['createDate'];
        // let date = this.commfunc.getNowFormatDate();
        date = this.commfunc.transDateN(date);
        let date2 = date.substr(5, 2);
        let year = date.substr(0, 4);
        let month1 = Number(date2);
        let day = 0;
        if (month1 == 1 || month1 == 3 || month1 == 5 || month1 == 7 || month1 == 8 || month1 == 10 || month1 == 12) {
            day = 31;
        } else if (month1 == 4 || month1 == 6 || month1 == 8 || month1 == 9 || month1 == 11) {
            day = 30;
        } else if (month1 == 2) {
            if (Number(year) % 4 == 0) {
                day = 29;
            } else {
                day = 28;
            }
        }
        let month = month1.toString();
        if (month.length == 1) {
            month = 0 + month;
        }
        this.bean['startDate'] = year + '.' + month + '.01';
        this.bean['endDate'] = year + '.' + month + '.' + day;
    }

    paginate(event) {
        // 分页
        this.bean.pageSize = Number(event.rows);
        let currentPage = event.page + 1;
        this.bean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.bean.pageSize;
        // this.queryList();
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret
    }


}