import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message, ConfirmationService, ConfirmDialogModule } from 'primeng/primeng';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { Router } from '@angular/router';

import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';
import { SupplyChainPlatformAdd, supplyChainPlatformList, supplyChainPlatformDetail, supplyChainPlatformModify, supplyChainPlatformDelete } from 'app/pages/tzb/customCenter/project-main-manage/task-center/bean/task-report.bean';
import { CreditReportHead } from 'app/pages/tzb/customCenter/project-main-manage/task-center/bean/credit-report.bean';
import { TZB_CUS_DOWNLOAD } from '../../../../../../@uisftech/common/service/http.constant';
import { DatePipe } from '@angular/common';
@Component({
    selector: "credit-report",
    templateUrl: './credit-report.component.html',
    styleUrls: ['./credit-report.component.scss'],
    providers: [Commfunc, ConfirmationService, DatePipe]
})

export class CreditReport implements OnInit {
    ch: any = CALENDAR_CN;
    code: any;
    first: number = 0;
    total: any;
    dataTable: any[];
    msgs: any;
    bean: supplyChainPlatformList = new supplyChainPlatformList();
    deleteBean: supplyChainPlatformDelete = new supplyChainPlatformDelete();
    detailBean: supplyChainPlatformDetail = new supplyChainPlatformDetail();
    tableHead: any[];
    orgTreeDisplay: boolean = false;
    orgInValue: string;
    user: any;
    constructor(
        public router: Router,
        public http: ProjectHttpService,
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService,
        public datePipe: DatePipe,
    ) {
        this.codeValues();
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.bean['orgId'] = this.user.extParam.orgId;
    }

    ngOnInit() {
        this.tableHead = CreditReportHead;
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.queryList();
    }

    query() {
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.first = 0;
        setTimeout(() => {
            this.queryList();
        }, 300)
        // this.queryList();
    }

    queryList() {
        this.msgs = [];
        if (this.bean['overDayStart'] != null) {
            if (!this.bean['overDayEnd']) {
                this.msgs.push({ severity: 'info', summary: '提示', detail: '超时天数为区间查询！' });
                return;
            }
        } else if (this.bean['overDayEnd'] != null) {
            if (!this.bean['overDayStart']) {
                this.msgs.push({ severity: 'info', summary: '提示', detail: '超时天数为区间查询！' });
                return;
            }
        }
        if (this.bean['pushDate']) {
            this.bean['pushDate'] = this.datePipe.transform(this.bean['pushDate'], 'yyyy-MM-dd');
        }
        if (this.bean['handDate']) {
            this.bean['handDate'] = this.datePipe.transform(this.bean['handDate'], 'yyyy-MM-dd');
        }
        let param = this.bean;
        this.http.creditInfoQuery(param).subscribe(data => {
            if (data['returnCode']['code'] == 'success') {
                this.dataTable = data['creditInfoList'];
                this.total = data['total'];
                console.log(this.dataTable);
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            }
            else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }

    export() {
        this.msgs = [];
        let param = this.bean;
        console.log(this.bean);
        this.http.downLodeCreditInfoQuery(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                window.open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(data.fileName) + "&fileUrl=" + data.fileUrl);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        });
    }

    //选择机构
    chooseOrg() {
        document.body.style.overflow = 'hidden';
        this.bean['orgId'] = this.user.extParam.orgId;
        this.orgInValue = this.bean['orgId'];
        this.orgTreeDisplay = !this.orgTreeDisplay;
    }

    //机构树回调
    treeCall(param) {
        document.body.style.overflow = 'auto';
        this.orgTreeDisplay = false;
        this.bean['orgId'] = param.orgId;
    }

    clear() {
        this.bean = new supplyChainPlatformList();
        this.bean['orgId'] = this.user.extParam.orgId;
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.first = 0;
        this.queryList();
    }

    paginate(event) {
        // 分页
        this.bean.pageSize = event.rows * 1;
        let currentPage = event.page + 1;
        this.bean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.bean.pageSize;
        this.queryList();
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret
    }

    // 自定义码值是否超时
    //  0:未超时
    //  1:超时
    //  2-超时霸屏

    //  任务状态 
    //  0-未处理
    //  1-已处理
    //  2-已取消
    //  3-已挂起

    codeValue = {
        isTimeoutOptions: [
            { label: '请选择', value: '' },
            { label: '未超时', value: '0' },
            { label: '超时', value: '1' },
            { label: '超时霸屏', value: '2' }
        ],
        taskStatusOptions: [
            { label: '请选择', value: '' },
            { label: '流转中', value: '0' },
            { label: '已结束', value: '1' }
        ]
    }
}