import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import { ActivatedRoute } from '@angular/router';
import { TZB_CUS_DOWNLOAD, TZB_HTTP_COM1 } from 'app/@uisftech/common/service/http.constant';
import { projectElementGroupDetailQuery, coopProjectTypeDetailQuery, coopProjectSet } from '../bean/project-protocol-query.bean';
import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';
import { CoopProjectTypeListQueryBean } from 'app/pages/tzb/customCenter/project-main-manage/project-main-manage-add/feasibility-query/bean/feasibility-query.bean';
// import { EmitServicer } from 'app/pages/tzb/customCenter/project-main-manage/private-components/sunject-of-cooperation/save.server';

import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { ProcessInstanceManagementService } from 'app/pages/tzb/workflow/activiti-work-flow/process-instance/process-instance-management/process-instance-management.service';
import { HttpService } from 'app/pages/tzb/workflow/activiti-work-flow/common/service/http.service';
import { MessageService } from 'primeng/components/common/messageservice';
import * as $ from 'jquery';
@Component({
    selector: "cooperative-project",
    templateUrl: './cooperative-project.component.html',
    styleUrls: ['./cooperative-project.component.scss'],

    providers: [Commfunc, ProcessInstanceManagementService, HttpService, MessageService]
})

export class CooperativeProject implements OnInit {
    protocolContentType: any;
    options1: any[] = [];
    coopProjectTypeName: any;
    planMsg: any = '';
    methods: any[] = [];
    msgs: any[];
    elementGroupId: any;
    headerMsg: any;
    method: any = '01';
    changeType: any = '01';
    type: any;
    projectId: any;
    coopProjectTypeId: any;
    typeName: any;
    bean: projectElementGroupDetailQuery = new projectElementGroupDetailQuery();
    msgBean: coopProjectTypeDetailQuery = new coopProjectTypeDetailQuery();
    saveBean: coopProjectSet = new coopProjectSet();
    coopProjectTypeListQueryBean: CoopProjectTypeListQueryBean = new CoopProjectTypeListQueryBean();
    updateValue: any;
    recoverkey: string = '001';

    code: any;

    constructor(
        public activatedRoute: ActivatedRoute,
        public http: ProjectHttpService,
        // public emitServicer:EmitServicer

        public commfunc: Commfunc,
        public httpService: CustomeHttpService,
        public httpService1: HttpService,
        private processInstanceManagementService: ProcessInstanceManagementService,
    ) {
        activatedRoute.queryParams.subscribe(queryParams => {
            debugger
            if (this.activatedRoute.snapshot.params.from == 'reminder-do') {
                this.type = '1';
                let t = this.commfunc.getSessionDataCH('cooperative-project-from-reminder-do');
                t = JSON.parse(t);
                let dataMap = JSON.parse(t['dataMap']);
                if (dataMap['planId']) {
                    this.planMsg = dataMap;
                    this.protocolContentType = this.planMsg['protocolContentType']
                } else {
                    this.planMsg = null
                }
                if (dataMap['bizPkFieldMap']) {
                    // 判断新增/追回
                    this.recoverkey = '666';
                    this.msgBean.coopProjectTypeId = dataMap['bizPkFieldMap']['coopProjectTypeId'];
                    this.coopProjectTypeId = dataMap['bizPkFieldMap']['coopProjectTypeId'];
                } else {
                    this.msgBean.coopProjectTypeId = dataMap['coopProjectTypeId'];
                    this.coopProjectTypeId = dataMap['coopProjectTypeId'];
                }
            } else {
                this.type = '2';
                // this.planMsg=queryParams['dataMap'];
                let dataMap = queryParams['bizPkFieldMap']['coopProjectTypeId']
                // this.planMsg = dataMap;
                // this.projectId = activatedRoute['snapshot']['params']['projectIdprojectId'];
                // this.msgBean.coopProjectTypeId = activatedRoute['snapshot']['params']['coopProjectTypeId'];
                // this.coopProjectTypeId = activatedRoute['snapshot']['params']['coopProjectTypeId'];
                // //console.log(this.planMsg)
            }
        })
    }
    ngOnInit() {
        this.codeValues();
        this.getquery();
        this.getMsg();
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    getMsg() {
        let _self = this;
        let url = `${TZB_HTTP_COM1}coopProjectTypeDetailQuery`
        let data = JSON.stringify(this.httpService1.handleParams(this.msgBean))

        $.ajax({
            url,
            datatype: "json",
            async: false,
            data,
            type: 'POST',
            headers: {
                'x-ajax': '1',
                'Content-Type': 'application/json'
            },
            success: function (result) {
                if (result) {
                    // _self.codeDatas = result;
                    _self.elementGroupId = result['elementGroupId'];
                    _self.coopProjectTypeName = result['coopProjectTypeName'];
                    _self.updateValue = {
                        coopProjectTypeName: _self.coopProjectTypeName,
                        planId: _self.planMsg['planId'],
                        planName: _self.planMsg['planName'],
                        custId: _self.planMsg['custId'],
                        custName: _self.planMsg['custName'],
                        planContent: _self.planMsg['planContent']
                    }
                }

            }, error: function (err) {
                _self.msgs = [{ severity: 'error', summary: '提示', detail: '查询失败' }]
            }

        })
    }
    click() {
        let arr = this.planMsg['protocolContent'].split('/');
        let path = '';
        for (let j = 1; j < arr.length - 1; j++) {
            path += '/' + arr[j];
        }
        path = path + '/';
        let donwname = arr[arr.length - 1];
        open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(donwname) + "&fileUrl=" + path, '_blank');
    }
    getquery() {
        this.http.coopProjectTypeDetailQuery(this.msgBean).subscribe(data => {
            if (data['returnCode']['code'] == 'success') {
                this.elementGroupId = data['elementGroupId'];
                this.coopProjectTypeName = data['coopProjectTypeName'];
            } else {
            }
        }, err => {
        })
    }

}