//新增
export class SupplyChainPlatformAdd {
    platformIdentify: string;             // 平台识别码
    platformName: string;                 // 平台名称
    platformDesc: string;                 // 平台描述
    platformCtrl: string;                 // 平台参数控制
    coopMode: string;                     //线上线下标志

    signFeedback: string;                 // 签约是否反馈
    projectApproveNo: string;             // 立项否决反馈
    projectApproveYes: string;            // 立项通过反馈
    beforeCreditFeedback: string;          // 预授信反馈
    creditFeedback: string;               // 授信反馈
    shipmentFlag: string;                 //发货控制
    payFeedback: string;                  //代付反馈
    repayFeedback: string;                //还款反馈

    pageNum: number;
    pageSize: number;
}
