import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message, ConfirmationService, ConfirmDialogModule } from 'primeng/primeng';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { Router, ActivatedRoute } from '@angular/router';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';

import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';
import { applyApproalCoreEnterprisesAdd, coreEnterprisesInfListQuery, coreEnterprisesInfoDetail } from './bean/core-customers.bean';
import { applyApproalCoreEnterprisesModify, coreEnterprisesInfDelete } from './bean/core-customers.bean';


@Component({
    selector: "core-customers",
    templateUrl: './core-customers.component.html',
    styleUrls: ['./core-customers.component.scss'],
    providers: [Commfunc, ConfirmationService]
})

export class CoreCustomers implements OnInit {
    typekey: any = '0';
    outVal: any;
    dealPerShow: boolean;
    waitSelUserList: any;
    coreEnterprisesNo: any;
    oldorgId: any;
    orgTreeDisplay: boolean = false;
    code: any;
    first: any = 1;
    total: any;
    dataTable: any[];
    msgs: any;
    bean: coreEnterprisesInfListQuery = new coreEnterprisesInfListQuery();
    deleteBean: coreEnterprisesInfDelete = new coreEnterprisesInfDelete();
    detailBean: coreEnterprisesInfoDetail = new coreEnterprisesInfoDetail();
    constructor(
        public router: Router,
        public http: ProjectHttpService,
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService,
        public httpWork: CustomeHttpService,
        public activatedRoute: ActivatedRoute,
    ) {
        this.codeValues();
    }



    ngOnInit() {
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.queryList()
    }

    query() {
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.queryList()
    }

    queryList() {
        this.msgs = [];
        this.http.coreEnterprisesInfListQuery(this.bean).subscribe(data => {
            if (data['returnCode']['code'] == 'success') {
                this.dataTable = data['returnList'];
                this.total = data['total'];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            }
            else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }

    clear() {
        this.bean = new coreEnterprisesInfListQuery();
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.queryList()
    }

    detail(e) {
        this.router.navigate(['/pages/tzb/custom-center/project-main-manage/core-customers-details'], { queryParams: { coreEnterprisesNo: e['coreEnterprisesNo'] } })
    }

    // 删除
    delete(i) {
        this.confirmationService.confirm({
            message: '将进行删除操作，是否继续?',
            accept: () => {
                this.msgs = [];
                this.deleteBean.coreEnterprisesNo = i['coreEnterprisesNo'];
                this.http.coreEnterprisesInfDel(this.deleteBean).subscribe(data => {
                    if (data['returnCode']['code'] == "success") {
                        this.queryList();
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                    } else {
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
                })
            }
        })

    }

    // 修改
    update(e) {
        this.router.navigate(['/pages/tzb/custom-center/project-main-manage/core-customers-add-modify'], { queryParams: { coreEnterprisesNo: e['coreEnterprisesNo'], type: '2' } })
    }
    // 新增
    newAdd() {
        this.router.navigate(['/pages/tzb/custom-center/project-main-manage/core-customers-add-modify'], { queryParams: { type: '1' } })
    }
    // 分配
    powerList(e) {
        this.orgTreeDisplay = true;
        this.oldorgId = e.branchId
        this.coreEnterprisesNo = e['coreEnterprisesNo']
    }

    // 提交
    submit(i) {
        this.nextDom();
        this.coreEnterprisesNo = i['coreEnterprisesNo']
    }
    // 提交后台录入数据
    refer() {
        let param = {
            operateType: '2',
            coreEnterprisesNo: this.coreEnterprisesNo,
        }
        this.http.applyApproalCoreEnterprises(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.queryList()
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }

            let c = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (c.extParam.specifyNextOpers || c.extParam.specifyNextNode) {
                delete c.extParam.specifyNextOpers;
                delete c.extParam.specifyNextNode;
                this.commfunc.setSessionData('commonHeader', JSON.stringify(c));
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
    }

    outDisplayCall(e) {
        this.dealPerShow = e;
    }

    // 审批选人
    nextDom() {
        this.msgs = [];
        let params = {
            userIdMap: {
                userId: JSON.parse(this.commfunc.getSessionData('commonHeader')).userId,
                userPost: JSON.parse(this.commfunc.getSessionData('commonHeader')).extParam.postId,
            },
            proxessDesc: '选人策略',
            processCode: 'GYLHXQYSP',
            bussinessMap: {

            }
        }
        this.httpWork.bpmFindNextNodeInfo(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.result.nextOutData && data.result.nextOutData.length >= 1 && data.result.nextOutData[0].selectUserList && data.result.nextOutData[0].selectUserList.length >= 1) {
                    this.dealPerShow = true;
                    this.outVal = data['result']['nextOutData'];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                } else {
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '所要选择人员没有配置' });
                }
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }
    //选人下一步
    outValueCall(e) {
        let persons = '';
        let specifyNextNode = '';
        e.forEach(el => {
            persons += el['tellerId'] + ',';
            specifyNextNode = el.outFlowId;
        });
        let c = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        c.extParam['specifyNextOpers'] = persons;
        c.extParam['specifyNextNode'] = specifyNextNode;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(c));

        this.refer();
    }

    // 分配企业客户
    Call(param) {
        this.orgTreeDisplay = param['orgTreeDisplay'];
        this.queryList()
    }

    paginate(event) {
        // 分页
        this.bean.pageSize = event.rows;
        let currentPage = event.page + 1;
        this.bean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.bean.pageSize;
        this.queryList();
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
}