import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { TZB_HTTP_XIN } from 'app/@uisftech/common/service/http.constant';
import { Router, ActivatedRoute } from '@angular/router';
import { SupplyChainHttpService } from '../../http/supply-chain.http.service';

@Component({
    selector: 'custInfo-table-detail',
    templateUrl: './custInfo-table-detail.component.html',
    styleUrls: ['./custInfo-table-detail.component.scss'],
    providers: [Commfunc, SupplyChainHttpService]
})
/**客户信息表 */
export class CustomInfoTableDetail implements OnInit {
    dataId: any;
    platformName: any;
    type: any = '0';
    mainFlowNo: any;
    @Input() inValue: any;
    msgs: Message[] = []; //提示信息
    dataList: any = {};
    code: any;
    SignStatus: any = [];//签约状态
    ch: any = CALENDAR_CN;
    CorporationCode: any = [];
    first: number = 0;//分页控制
    totalNum: number;
    signDate: Date = null;
    recommendDate: Date = null;
    IdType: any = [];//证件类型
    projectId: string;
    custId: string;
    constructor(
        public projectHttpService: SupplyChainHttpService,
        public commfunc: Commfunc,
        public activatedRoute: ActivatedRoute,

    ) {
        this.codeValues() //调用方法，获取全部码值
        this.SignStatus = this.code['SIGNSTATUS'];//签约状态
        this.IdType = this.code['ALL_IDEN_TYPE']; //证件类型
        this.activatedRoute.queryParams.subscribe(queryParams => {
            debugger
            if (JSON.stringify(queryParams) == '{}' && JSON.stringify(this.activatedRoute['snapshot']['params']) != '{}') {
                let detprm = JSON.parse(this.activatedRoute['snapshot']['params']['dataMap'])
                this.type = '1';
                this.projectId = detprm['projectId'];
                this.custId = detprm['custId'];
                if (detprm['platformName']) {
                    this.platformName = detprm['platformName']
                    this.dataId = detprm['dataId']
                } else {
                }
                if (this.activatedRoute.snapshot.params.mainFlowNo) {
                    this.mainFlowNo = this.activatedRoute.snapshot.params.mainFlowNo;
                }
            }
        })
    }

    ngOnInit() {
        let param;
        if (this.type == '1') {
            param = {
                projectId: this.projectId,
                custId: this.custId,
                dataId: this.dataId
            }
            this.hisquery(param)
        } else {
            param = {
                projectId: this.inValue.projectId,
                custId: this.inValue.clientNo,
            }
            this.platformName = this.inValue.platformName
            this.query(param)
        }
    }
    //查询
    query(item) {
        this.projectHttpService.supplyChainCustInfo(item).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.dataList = data.resultMap;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
    }
    //查询我的历史里已删除详情
    hisquery(item) {
        this.projectHttpService.supplyChainQueryDelCustInfo(item).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.dataList = data.resultMap;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    goback() {
        window.history.go(-1)
    }


}
