import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message, ConfirmationService, ConfirmDialogModule } from 'primeng/primeng';
import { Router } from '@angular/router';
import { CoopProjectTypeListQueryBean, CoopProjectTypeSetBean } from './bean/project-creation.bean';
import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';

@Component({
    selector: "project-creation",
    templateUrl: './project-creation.component.html',
    styleUrls: ['./project-creation.component.scss'],
    providers: [Commfunc, ConfirmationService]
})

export class ProjectCreation implements OnInit {
    creat_user: any;
    coopProjectTypeListQueryBean: CoopProjectTypeListQueryBean = new CoopProjectTypeListQueryBean();
    coopProjectTypeSetBean: CoopProjectTypeSetBean = new CoopProjectTypeSetBean();
    msgs: any[];
    unval: any[] = [];
    list1val: any = '';
    list2val: any = '';
    list3val: any = '';

    list1: any[] = [{
        label: '供应链融资',
        value: 'GR',
    }, {
        label: '供应链获客',
        value: 'G10004',
    }];
    list2: any[] = [{
        label: '供应链金融(下游)',
        value: 'GRX',
    }, {
        label: '供应链金融(上游)',
        value: 'GRS',
    }];
    // GRX 下游
    list21: any[] = [{
        label: '供应链白条(贷款)',
        value: 'G10001',
    }];
    // GRS 上游
    list22: any[] = [{
        label: '应收账款质押融资',
        value: 'G10003',
    }];
    constructor(
        public router: Router,
        public http: ProjectHttpService,
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService,
    ) {

    }
    ngOnInit() {
        let first = { label: '请选择' }
        this.unval.unshift(first);
        this.list1.unshift(first);
        this.list2.unshift(first);
        this.list21.unshift(first);
        this.list22.unshift(first);
    }
    querylist(i) {
        let pamarm;
        if (i == '1') {
            if (this.list1val == 'GR') { //供应链融资
                this.list2val = '';
                this.list3val = '';
                pamarm = '';
            } else if (this.list1val == 'G10004') { //供应链获客
                pamarm = this.list1val;
            } else {
                pamarm = '';
            }
        } else if (i == '2') {
            this.list3val = '';
            pamarm = '';
        } else if (i == '3') {
            pamarm = this.list3val
        }
        this.coopProjectTypeSetBean.coopProjectTypeId = pamarm;
    }
    //创建判断按钮
    buildBtn() {
        let pamarm = this.coopProjectTypeSetBean.coopProjectTypeId;
        this.msgs = [];
        if (pamarm == '' || pamarm == undefined || pamarm == null) {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择项目类型进行流程创建' });
            return;
        } else {
            this.confirmationService.confirm({
                message: '将发起项目创建流程，是否继续?',
                accept: () => {
                    console.log(pamarm)
                    this.next();
                }
            });
        }

    }
    next() {
        this.creat_user = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
        this.msgs = []
        if (this.coopProjectTypeSetBean.coopProjectTypeId != null || this.coopProjectTypeSetBean.coopProjectTypeId != undefined) {
            this.coopProjectTypeSetBean.operateType = '4';
            let pram = {
                coopProjectTypeId: this.coopProjectTypeSetBean.coopProjectTypeId,
                operateType: this.coopProjectTypeSetBean.operateType
            }
            this.http.coopProjectTypeSet(pram).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                    let pamarm = {
                        processId: data.processId,
                        userId: this.creat_user,
                        startuserId: this.creat_user,
                        curPage: 1,
                        pageRows: 20,
                    };
                    this.http.bpmQueryUnDoTaskList(pamarm).subscribe(data => {
                        if (data.returnCode.code == 'success') {
                            console.log(data);
                            let parem = data.pageData.result[0];

                            this.commfunc.toDisposeSchedule(parem);
                        } else {
                            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                        }
                    }, err => {
                        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
                    })
                } else {
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
            })
        } else {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择' });
        }
    }
}