import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Message, ConfirmationService, } from 'primeng/primeng';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { Router } from '@angular/router';
import { ProductManageHttpService } from '../../../../../product-manage/http/product-manage.http.service';


@Component({
    selector: "project-theme",
    templateUrl: './project-theme.component.html',
    styleUrls: ['./project-theme.component.scss'],
    providers: [ConfirmationService, Commfunc, ProductManageHttpService]
})

export class ProjectTheme implements OnInit {
    startTime: any;
    sateType: any;
    personName: any;
    detailDisplay: boolean = false;
    startDate: Date = null;
    productId: any;
    endDate: Date = null;
    proName: any;
    ch: any = CALENDAR_CN;
    msgs: any;

    options1: any[] = [
        {
            label: '额度调整',
            value: '01'
        },
        {
            label: '担保方式调整',
            value: '02'
        },
    ];
    methods: any[] = [
        {
            label: '无',
            value: '01'
        },
        {
            label: '保证金',
            value: '02'
        },
    ];
    method: any = '01';
    changeType: any = '01';
    constructor(
        public confirmationService: ConfirmationService,
        public productPlatformHttpService: ProductManageHttpService,
        public router: Router,
    ) {

    }
    ngOnInit() {

    }
    perTreeShow(t) {
        this.sateType = t
        this.detailDisplay = true
    }
    perInfor(e) {
        console.log(e)
        this.personName = e.tellerName;
        this.detailDisplay = false;
    }
    outValue(data) {

        this.proName = data[0].productName;
        this.detailDisplay = false;
    }
    //手动输入日期时，失去焦点时进行判断
    onchange1() {
        this.showDataStart()
    }
    onchange2() {
        this.showDataEnd()
    }
    choicesaleUser(e) {
        console.log(e)
        this.detailDisplay = false;
        let product = new Array();
        e.depart.forEach(element => {
            product.push(element.postName)
        });

        var productID = product.join(',')

        this.productId = productID;
        console.log(productID);
    }
    assoctate(e) {
        let string = ""
        for (let i = 0; i < e.length; i++) {
            string += e[i].productName + ',';
        }
        this.proName = string;
        this.detailDisplay = false;
    }
    sumbit() {
        this.router.navigate(['/pages/tzb/custom-center/project-main-manage/project-main-manage-add'], { queryParams: { state: '2' } })
    }
    save() {
        this.router.navigate(['/pages/tzb/custom-center/project-main-manage/project-main-manage-add'], { queryParams: { state: '2' } })
    }
    //开始日期
    showDataStart() {
        if (this.startDate && this.endDate) {
            if (this.startDate > this.endDate) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
                setTimeout(() => {
                    this.startDate = null;
                })
            }
        }
    }
    //结束日期
    showDataEnd() {
        if (this.startDate && this.endDate) {
            if (this.startDate > this.endDate) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
                setTimeout(() => {
                    this.endDate = null;
                })
            }
        }
    }

}