import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message, ConfirmationService, ConfirmDialogModule } from 'primeng/primeng';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { Router } from '@angular/router';

import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';
import { SupplyChainPlatformAdd, supplyChainPlatformList, supplyChainPlatformDetail, supplyChainPlatformModify, supplyChainPlatformDelete } from 'app/pages/tzb/customCenter/project-main-manage/task-center/bean/task-report.bean';
import { ActivatedRoute } from '@angular/router';



@Component({
    selector: "month-report-word",
    templateUrl: './month-report-word.component.html',
    styleUrls: ['./month-report-word.component.scss'],
    providers: [Commfunc, ConfirmationService]
})

export class MonthReportWord implements OnInit {
    createDate: any;
    reportName: any;
    reportId: any;
    ch: any = CALENDAR_CN;
    code: any;
    first: any = 1;
    total: any;
    dataTable1: any[];
    dataTable2: any[];
    dataTable3: any[];
    msgs: any;
    bean: supplyChainPlatformList = new supplyChainPlatformList();
    deleteBean: supplyChainPlatformDelete = new supplyChainPlatformDelete();
    reportCode: string;
    item: any;
    legacy: any;

    constructor(
        public router: Router,
        public http: ProjectHttpService,
        public commfunc: Commfunc,
        public activatedRouter: ActivatedRoute,
        public confirmationService: ConfirmationService,
    ) {
        this.codeValues();
        activatedRouter.queryParams.subscribe(queryParams => {
            this.bean['reportId'] = queryParams.partyOrGroupNo;
            this.bean['reportName'] = queryParams.partyOrGroupName;
            this.bean['createDate'] = queryParams.createDate;
            this.reportId = queryParams.partyOrGroupNo;
            this.reportName = queryParams.partyOrGroupName;
            this.createDate = queryParams.createDate;

        });
    }

    ngOnInit() {
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.TimeToDate();
        this.queryList(this.bean, '01');
        // this.queryList(this.bean, '02');
        // this.queryList(this.bean, '03');
    }

    // 数据查询
    queryList(param, num) {
        this.msgs = [];
        // this.bean['reportCode'] = num;
        let pram = {
            reportCode: num,
            reportId: this.reportId,
            reportName: this.reportName,
            createDate: this.createDate,
        }
        this.http.taskReportQuery(pram).subscribe(data => {
            if (data['returnCode']['code'] == 'success') {
                if (num == '01') {
                    this.processData(1, data.taskReportList);
                    this.queryList(this.bean, '02')
                } else if (num == '02') {
                    this.processData(2, data.taskReportList);
                    this.queryList(this.bean, '03')
                } else if (num == '03') {
                    console.log(data.taskReportList);
                    this.processData(3, data.taskReportList);
                }
                console.log(data.taskReportList);
                this.total = data['total'];
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }

    // 处理数据
    processData(num, data) {
        let legacy = [];
        let itemDisplay = true;
        let legacyDisplay = true;
        let display = true;
        if (!data.length) {
            this.item = 0;
            this.legacy = 0;
            display = false;
        } else {
            for (let i = data.length - 1; i >= 0; i--) {

                if (data[i]['itemCode'] == 'DIAG_HEAD') {
                    if (data[i]['figure1'] == 0) {
                        itemDisplay = false;
                    }
                    if (data[i]['figure2'] == 0) {
                        legacyDisplay = false;
                    }
                    if (data[i]['figure3'] == 0) {
                        this.bean['receiveDisplay'] = false;
                    }
                    this.item = data[i]['figure1'];
                    this.legacy = data[i]['figure2'];
                    if (data[i]['figure3']) {
                        this.bean['receive'] = data[i]['figure3'];
                    }
                    data.splice(i, 1);
                }
                if (!data[i]) {
                    if (data[i - 1]['itemCode'] == 'DIAG_TOTAL') {
                        legacy = data.splice(i, 1);
                    }
                } else {
                    if (data[i]['itemCode'] == 'DIAG_TOTAL') {
                        legacy = data.splice(i, 1);
                    }
                }

            }
            if (legacy.length) {
                data.push(legacy[0]);
            }
            data.forEach((el, index) => {
                if (el.figure3 && el.figure3 < el.figure4) {
                    el['figureFlag'] = 'down'
                } else if (el.figure3 && el.figure3 > el.figure4) {
                    el['figureFlag'] = 'up'
                } else {
                    el['figureFlag'] = 'none'
                }
            });

        }
        if (num == 1) {
            this.bean['item1'] = this.item;
            this.bean['legacy1'] = this.legacy;
            this.bean['display1'] = display;
            this.bean['itemDisplay1'] = itemDisplay;
            this.bean['legacyDisplay1'] = legacyDisplay;
            this.dataTable1 = data;
        } else if (num == 2) {
            this.bean['item2'] = this.item;
            this.bean['legacy2'] = this.legacy;
            this.bean['display2'] = display;
            this.bean['itemDisplay2'] = itemDisplay;
            this.bean['legacyDisplay2'] = legacyDisplay;
            this.dataTable2 = data;
        } else if (num == 3) {
            this.bean['item3'] = this.item;
            this.bean['legacy3'] = this.legacy;
            this.bean['display3'] = display;
            this.bean['itemDisplay3'] = itemDisplay;
            this.bean['legacyDisplay3'] = legacyDisplay;
            this.dataTable3 = data;
        }
    }
    // 点击事项小类数字去待办
    appview(dalist, typ) {
        if (this.commfunc.getSessionDataCH('workliuCode')) {
            this.commfunc.clearSession('workliuCode');
            this.commfunc.clearSession('workliuData');
        }
        this.router.navigate(["/pages/tzb/custom/reminder-do/backlog-details/btn-page2/work-none"], { queryParams: { flag: 2, totalNum: 7, none: 'Y', startDate: this.bean['startDate'], endDate: this.bean['endDate'] } })
    }
    TimeToDate() {
        let date = this.bean['createDate'];
        // let date = this.commfunc.getNowFormatDate();
        date = this.commfunc.transDateN(date);
        let date2 = date.substr(5, 2);
        let year = date.substr(0, 4);
        let month1 = Number(date2);
        let day = 0;
        if (month1 == 1 || month1 == 3 || month1 == 5 || month1 == 7 || month1 == 8 || month1 == 10 || month1 == 12) {
            day = 31;
        } else if (month1 == 4 || month1 == 6 || month1 == 8 || month1 == 9 || month1 == 11) {
            day = 30;
        } else if (month1 == 2) {
            if (Number(year) % 4 == 0) {
                day = 29;
            } else {
                day = 28;
            }
        }
        let month = month1.toString();
        if (month.length == 1) {
            month = 0 + month;
        }
        this.bean['startDate'] = year + '.' + month + '.01';
        this.bean['endDate'] = year + '.' + month + '.' + day;
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret
    }

}