import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import { Router, ActivatedRoute } from '@angular/router';
import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';
import { coopProjectElementQuery, coopProjectElementAdjust } from '../bean/project-protocol-query.bean';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { nextBean } from 'app/pages/tzb/customCenter/project-main-manage/private-components/sunject-of-cooperation/bean/sunject-of-cooperation.bean';

@Component({
    selector: "reason-opinion",
    templateUrl: './reason-opinion.component.html',
    styleUrls: ['./reason-opinion.component.scss'],
})

export class ReasonOpinion implements OnInit {
    mainFlowNo: any;
    projectName: any;
    adjustSeqno: any;
    coopModeName: any;
    bizType: any;
    projectEndDate: any;
    projectBeginDate: any;
    coopMode: any;
    adjustOpinionkey: string;
    adjustReasonkey: string;
    userId: any;
    user: any;
    postId: any;
    adjustOpinion: any;
    adjustReason: any;
    detilells: any;
    detilell: any;
    statet: any;
    custName: any;
    custId: any;

    afterAdjust1: any;
    org: any;
    options1: any[] = [];
    methods: any[] = [];
    msgs: any[];
    code: any;
    changeBean: coopProjectElementQuery = new coopProjectElementQuery();
    postBean: coopProjectElementAdjust = new coopProjectElementAdjust();
    method: any = '01';
    projectId: any;
    changeType: any = '01';
    beforeAdjust: any;
    beforeAdjust1: any;
    ch: any = CALENDAR_CN;
    outVal: any;
    NextBean: nextBean = new nextBean()
    dealPerShow: boolean;
    // 选人
    waitSelUserList: any;
    aaaa: boolean = false;
    queryStatus: any = [];
    strategyId: string = '';
    typekey: any = '0';
    isdisabled: boolean = false;

    theRequest: any = [];
    isConmit: boolean = false;

    constructor(
        public router: Router,
        public activatedRoute: ActivatedRoute,
        public http: ProjectHttpService,
        public commfunc: Commfunc,
        public httpWork: CustomeHttpService,
        public customService: CustomeHttpService,
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user.extParam.postId) {
            this.postId = this.user.extParam.postId;
        }
        this.NextBean.taskId = activatedRoute.snapshot.params.taskId;
        this.NextBean.userId = this.userId;
        this.NextBean.postId = this.postId;
    }

    ngOnInit() {
        this.codeValues();
        let url = location.href;  //获取url中？后面的参数
        if (url.indexOf(";") != -1) {
            let str = url.substr(1);
            let strs = str.split(";");
            for (let i = 0; i < strs.length; i++) {
                this.theRequest[strs[i].split("=")[0]] = (strs[i].split("=")[1]);
            }
        }
        // 查询审批记录
        if (this.theRequest.taskRate == '1') {
            this.isConmit = true;
        } else {
            this.isConmit = false;
        }

        this.org = JSON.parse(this.commfunc.getSessionData('officeOrgId'));
        this.NextBean.userId = this.org;
        this.activatedRoute.queryParams.subscribe(queryParams => {
            if (JSON.stringify(queryParams) == '{}') {
                this.typekey = '1';
                let t = this.commfunc.getSessionDataCH('ZZCX-from-reminder-do');
                t = JSON.parse(t);
                let datmap = JSON.parse(t['dataMap']);
                if (datmap['bizPkFieldMap']) {
                    this.custId = datmap['bizPkFieldMap']['custId']
                    this.custName = datmap['bizPkFieldMap']['custName']
                    this.adjustReason = datmap['bizPkFieldMap']['adjustReason']
                    this.adjustOpinion = datmap['bizPkFieldMap']['adjustOpinion']
                    this.statet = datmap['bizPkFieldMap']['adjustElement'];
                    this.projectId = datmap['bizPkFieldMap']['projectId']
                    this.adjustSeqno = datmap['bizPkFieldMap']['adjustSeqno'];
                }
                this.bizType = datmap.bizType;
                this.coopMode = datmap.coopMode;
                this.coopModeName = datmap.coopModeName;
                this.projectBeginDate = datmap.projectBeginDate;
                this.projectEndDate = datmap.projectEndDate;
                this.postBean.adjustReason = this.adjustReason;
                this.postBean.adjustOpinion = this.adjustOpinion;

                if (this.activatedRoute.snapshot.params.mainFlowNo) {
                    this.mainFlowNo = this.activatedRoute.snapshot.params.mainFlowNo;
                }
            } else {
                this.projectId = queryParams.projectId;
                this.custId = queryParams.custId;
                this.custName = queryParams.custName;
                this.statet = queryParams.statet;
            }
        })

        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s['globalSeqNo'] && this.typekey != '1') {
            delete s['globalSeqNo'];
            this.commfunc.setSessionData('commonHeader', JSON.stringify(s));
        }

    }

    goback() {
        if (this.typekey == '1') {
            window.history.go(-1);
        } else {
            this.router.navigate(['/pages/tzb/custom-center/project-main-manage/ProjectProtocolQuery'], { queryParams: { index: 1 } })
        }

    }

    save() {
        this.msgs = [];
        if (this.postBean.adjustReason == null || this.postBean.adjustReason == undefined || this.postBean.adjustReason == '') {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入原因' });
            return;
        }
        if (this.postBean.adjustOpinion == null || this.postBean.adjustOpinion == undefined || this.postBean.adjustOpinion == '') {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入意见' });
            return;
        }

        this.postBean.projectId = this.projectId;
        this.postBean.adjustElement = this.statet;
        this.adjustReasonkey = this.postBean.adjustReason;
        this.adjustOpinionkey = this.postBean.adjustOpinion;

        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.postId) {
            this.postId = s.extParam.postId;
        }
        if (this.typekey == '1') {
            s['globalSeqNo'] = this.mainFlowNo;
        }
        // 删除之前选中的人
        if (s.extParam.specifyNextOpers && s.extParam.specifyNextNode) {
            delete s.extParam.specifyNextOpers;
            delete s.extParam.specifyNextNode;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(s));
        }
        // 发起选人
        this.waitSelUserList = this.commfunc.query('YSCX', {
            adjustElement: this.statet,
            beforeAdjust: this.beforeAdjust1,
            afterAdjust: this.afterAdjust1
        })
        if (!this.waitSelUserList.erro) {
            this.outVal = this.waitSelUserList.waitSelUserList;
            if (this.waitSelUserList.display) {
                this.dealPerShow = true;
            } else {

            }
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
        }

    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret;
    }
    outDisplayCall(e) {
        this.dealPerShow = e;
    }
    outValueCall(e) {
        let persons = '';
        let specifyNextNode = '';
        e.forEach(el => {
            persons += el['tellerId'] + ',';
            specifyNextNode = el.outFlowId;
        });
        if (this.typekey == '1') {
            let params = {
                userId: this.userId,
                taskId: this.NextBean.taskId,
                bussinessMap: {
                    projectId: this.projectId,
                    specifyNextOpers: persons,
                    specifyNextNode: specifyNextNode,
                    userId: JSON.parse(this.commfunc.getSessionData('commonHeader')).userId,
                    adjustElement: this.statet,
                    beforeAdjust: '',
                    afterAdjust: '',
                    dataMap: {
                        bizPkFieldMap: {
                            adjustElement: this.statet,
                            adjustOpinion: this.adjustOpinionkey,
                            adjustReason: this.adjustReasonkey,
                            adjustSeqno: this.adjustSeqno,
                            beforeAdjust: '',
                            afterAdjust: '',
                            custId: this.custId,
                            custName: this.custName,
                            projectId: this.projectId,
                            projectName: this.projectName
                        },
                        projectId: this.projectId,
                        custId: this.custId,
                        custName: this.custName,
                        adjustElement: this.statet,
                        adjustReason: this.adjustReasonkey,
                        adjustOpinion: this.adjustOpinionkey,
                        projectEndDate: this.projectEndDate,
                        coopMode: this.coopMode,
                        projectBeginDate: this.projectBeginDate,
                        bizType: this.bizType,
                        coopModeName: this.coopModeName
                    },
                    adjustReason: this.adjustReasonkey,
                    adjustOpinion: this.adjustOpinionkey,
                }
            }
            this.customService.bpmNextTask(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                    this.goback()
                } else {
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
            })

        } else {
            let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            commonHeader.extParam['specifyNextOpers'] = persons;
            commonHeader.extParam['specifyNextNode'] = specifyNextNode;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));

            this.http.coopProjectElementAdjust(this.postBean).subscribe(data => {
                if (data['returnCode']['code'] == 'success') {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                    this.goback()
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers || s.extParam.specifyNextNode) {
                    delete s.extParam.specifyNextOpers;
                    delete s.extParam.specifyNextNode;
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
            })
        }


    }
}