import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { Router } from '@angular/router';
import { coopProjectListQueryBean, coopProjectSet } from './bean/project-protocol-query.bean';
import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { ProcessInstanceManagementService } from 'app/pages/tzb/workflow/activiti-work-flow/process-instance/process-instance-management/process-instance-management.service';
import { HttpService } from 'app/pages/tzb/workflow/activiti-work-flow/common/service/http.service';
import { MessageService } from 'primeng/components/common/messageservice';

@Component({
    selector: "project-protocol-query",
    templateUrl: './project-protocol-query.component.html',
    styleUrls: ['./project-protocol-query.component.scss'],
    providers: [CreditCcmService, ProcessInstanceManagementService, HttpService, MessageService]
})

export class ProjectProtocolQuery implements OnInit {
    creditApproveDisplay: boolean;
    processId: any;
    options1: any[] = [];
    bean: coopProjectListQueryBean = new coopProjectListQueryBean();
    deletBean: coopProjectSet = new coopProjectSet();
    total: any;
    first: any = 0;
    state: any = '1';
    startDate: Date = null;
    endDate: Date = null;
    ch: any = CALENDAR_CN;
    msgs: any;
    typeTitle: any;
    display: boolean = false;
    dialogHead: any = '新增';
    dialogType: any = '01';
    detailsDisplay: boolean = false;
    tableData: any[] = [];
    type: any;
    cooplist: any[] = [{
        label: '供应链白条(贷款)',
        value: 'G10001',
    }, {
        label: '应收账款质押融资',
        value: 'G10003',
    }, {
        label: '供应链获客',
        value: 'G10004',
    }];
    tableHead: any[] = [
        {
            label: '核心企业客户号',
            value: 'custId',
            wth: '150px'
        },
        {
            label: '核心企业名称',
            value: 'custName',
            wth: '150px'
        },
        {       //新增查询
            label: '供应链客户群号',
            value: 'crowdId',
            wth: '150px'
        },
        {       //新增查询
            label: '供应链模式',
            value: 'coopMode',
            wth: '150px'
        },
        {       //新增查询
            label: '项目协议额度(万元)',
            value: 'projectLimit',
            wth: '165px'
        },
        {
            label: '协议起始日期',
            value: 'beginDate',
            wth: '150px'
        },
        {
            label: '协议结束日期',
            value: 'endDate',
            wth: '150px'
        },
        {
            label: '审批状态、协议状态',
            value: 'projectStatus',
            wth: '165px'
        }, {       //新增查询
            label: '经办机构',
            value: 'handlingOrg',
            wth: '150px'
        },
    ];
    //码值
    code: any;
    coopMode: any[];
    projectStatus: any[];
    state101: string = '101';
    state102: string = '102';
    constructor(
        public router: Router,
        public http: ProjectHttpService,
        public commfunc: Commfunc,
    ) {

    }
    ngOnInit() {
        this.codeValues();
        this.bean.pageNum = 1;
        this.bean.pageSize = 10;
        this.queryList();
        this.coopMode = this.code['coopMode'];
        this.projectStatus = this.code['project_status'];
        let first = { label: '请选择' }
        this.cooplist.unshift(first);

    }
    copy(item) {
        this.router.navigate(['/pages/tzb/custom-center/project-main-manage/subject-details'], { queryParams: { projectId: item['projectId'],processId: item['processId'] } })
    }

    // 流程轨迹查看
    roadsee(item) {
        // item.processId '3575243'
        this.processId = item.processId;
        this.creditApproveDisplay = true;
    }
    // 关闭审批详情
    closeFirtproc() {
        this.creditApproveDisplay = false;
    }
    // 审批详情弹框回调方法
    creditApproveCall() {
        this.creditApproveDisplay = false;
    }
    update(item) {
        this.router.navigate(['/pages/tzb/custom-center/project-main-manage/subject-adjustment'], {
            queryParams: {
                projectId: item['projectId'],
                projectName: item['projectName'],
                custId: item['custId'],
                custName: item['custName'],
                lei: 110
            }
        })
    }
    // 撤销
    revoke(item) {
        this.router.navigate(['/pages/tzb/custom-center/project-main-manage/reason-opinion'], { queryParams: { projectId: item['projectId'], custId: item['custId'], custName: item['custName'], statet: this.state101 } })
    }
    // 中止
    suspend(item) {
        this.router.navigate(['/pages/tzb/custom-center/project-main-manage/reason-opinion'], { queryParams: { projectId: item['projectId'], custId: item['custId'], custName: item['custName'], statet: this.state102 } })
    }
    query() {
        this.bean.beginDate = this.commfunc.transDateN(this.startDate);
        this.bean.endDate = this.commfunc.transDateN(this.endDate);
        this.bean.pageNum = 1;
        this.bean.pageSize = 10;
        this.queryList()
    }
    clear() {
        this.bean = new coopProjectListQueryBean();
        this.bean.pageNum = 1;
        this.bean.pageSize = 10;
        this.startDate = null;
        this.endDate = null;
        this.queryList();
    }
    queryList() {
        this.msgs = [];
        this.bean.coopPartnerType = '01';
        this.http.coopProjectListQuery(this.bean).subscribe(data => {
            if (data['returnCode']['code'] == "success") {
                this.total = data['total'];
                this.tableData = this.agree(data['returnList']);
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });

            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }

    // 判断当前登录人权限--更改、中止
    agree(orglist) {
        let user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        let userId;
        if (user.userId) {
            userId = user.userId;
        }
        if (orglist) {
            orglist.forEach(e => {
                if (userId == e['permissionOrgList'][0] || userId == e['permissionOrgList'][1]) {
                    // this.agrees = true;
                    e['agrees'] = true;
                }
            });
            return orglist;
        }
    }

    delete(i) {
        this.deletBean.operateType = '3';
        this.deletBean.projectId = i['projectId'];
        this.http.coopProjectSet(this.deletBean).subscribe(data => {
            if (data['returnCode']['code'] == "success") {
                this.queryList();
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })

    }
    sumbit(i) {
        this.deletBean.operateType = '4';
        this.deletBean.projectId = i['projectId'];
        this.http.coopProjectSet(this.deletBean).subscribe(data => {
            if (data['returnCode']['code'] == "success") {
                this.queryList();
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    //开始日期
    showDataStart() {
        if (this.startDate && this.endDate) {
            if (this.startDate > this.endDate) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
                setTimeout(() => {
                    this.startDate = null;
                })
            }
        }
    }
    //结束日期
    showDataEnd() {
        if (this.startDate && this.endDate) {
            if (this.startDate > this.endDate) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
                setTimeout(() => {
                    this.endDate = null;
                })
            }
        }
    }
    //手动输入日期时，失去焦点时进行判断
    onchange1() {
        this.showDataStart()
    }
    onchange2() {
        this.showDataEnd()
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret
    }

    //分页
    paginate(event) {
        this.bean.pageSize = event.rows * 1;
        //当前页
        let currentPage = event.page + 1;
        this.bean.pageNum = currentPage;
        this.first = event.page * event.rows;
        /**
         * --2018.1.15
         */
        //调用查询的方法
        this.queryList();
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
}
