import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message, ConfirmationService, ConfirmDialogModule } from 'primeng/primeng';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { Router } from '@angular/router';

import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';
import { SupplyChainPlatformAdd, supplyChainPlatformList, supplyChainPlatformDetail, supplyChainPlatformModify, supplyChainPlatformDelete } from './bean/three-platform.bean';



@Component({
    selector: "three-platform",
    templateUrl: './three-platform.component.html',
    styleUrls: ['./three-platform.component.scss'],
    providers: [Commfunc, ConfirmationService]
})

export class ThreePlatform implements OnInit {
    code: any;
    first: any = 1;
    total: any;
    dataTable: any[];
    msgs: any;
    bean: supplyChainPlatformList = new supplyChainPlatformList();
    deleteBean: supplyChainPlatformDelete = new supplyChainPlatformDelete();
    detailBean: supplyChainPlatformDetail = new supplyChainPlatformDetail();
    constructor(
        public router: Router,
        public http: ProjectHttpService,
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService,
    ) {
        this.codeValues();
    }

    ngOnInit() {
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.queryList()
    }

    query() {
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.queryList()
    }

    queryList() {
        this.msgs = [];
        this.http.supplyChainPlatformList(this.bean).subscribe(data => {
            if (data['returnCode']['code'] == 'success') {
                this.dataTable = data['returnList'];
                this.total = data['total'];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            }
            else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }

    clear() {
        this.bean = new supplyChainPlatformList();
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.queryList()
    }
    // 详情
    detail(e) {
        //this.detailBean.platformIdentify = e['platformIdentify'];
        this.router.navigate(['/pages/tzb/custom-center/project-main-manage/three-platform-details'], { queryParams: { platformIdentify: e['platformIdentify'] } })
    }

    // 删除
    delete(i) {

        this.confirmationService.confirm({
            message: '将进行删除操作，是否继续?',
            accept: () => {
                this.msgs = [];
                this.deleteBean.platformIdentify = i['platformIdentify'];
                this.http.supplyChainPlatformDelete(this.deleteBean).subscribe(data => {
                    if (data['returnCode']['code'] == "success") {
                        this.queryList();
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                    } else {
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
                })
            }
        })

    }
    // 修改
    update(e) {
        this.router.navigate(['/pages/tzb/custom-center/project-main-manage/three-platform-add-modify'], { queryParams: { platformIdentify: e['platformIdentify'], type: '2' } })
    }
    // 新增
    newAdd() {
        this.router.navigate(['/pages/tzb/custom-center/project-main-manage/three-platform-add-modify'], { queryParams: { type: '1' } })
    }
    // 提交
    submit(i) {
        let param = {
            operateType: '2',
            platformIdentify: i['platformIdentify'],
        }
        this.http.supplyChainPlatformAdd(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.queryList()
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
    }

    paginate(event) {
        // 分页
        this.bean.pageSize = event.rows;
        let currentPage = event.page + 1;
        this.bean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.bean.pageSize;
        this.queryList();
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret
    }

}