import { Component, OnInit, Output,Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import { ActivatedRoute } from '@angular/router';
import { CoopProjectTypeListQueryBean } from './project-creation-qita/bean/project-creation-qita.bean';
import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';


@Component({
    selector:"project-main-manage-add-qita",
    templateUrl:'./project-main-manage-add-qita.component.html',
    styleUrls:['./project-main-manage-add-qita.component.scss'],
})

export class ProjectMainManageAddQita implements OnInit{
    index:any;
    msgs:any[];
    List:any[];
    //  @Input('ind') ind:any;
    coopProjectTypeListQueryBean:CoopProjectTypeListQueryBean=new CoopProjectTypeListQueryBean();
    constructor(
        private activatedRoute:ActivatedRoute,
        public http:ProjectHttpService,
        public commfunc: Commfunc,
    ){
        this.index='0'
        activatedRoute.queryParams.subscribe(queryParams=>{
            console.log(queryParams)
            this.index=queryParams.index;
        })
    }
    ngOnInit(){
        this.queryList();
    }
    queryList(){
        this.coopProjectTypeListQueryBean.pageSize=20000;
        this.coopProjectTypeListQueryBean.pageNum=1;
        // 删除之前选中的人
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextNode) {
          
          delete s.extParam.specifyNextNode;
          let commonHeader = s;
          this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.http.coopProjectTypeListQuery(this.coopProjectTypeListQueryBean).subscribe(data=>{
            if(data.returnCode.code=='success'){
                this.List=data['returnList'];
                this.List.forEach(e=>{
                    e.label=e.coopProjectTypeName;
                    e.value=e.coopProjectTypeId;
                })
                let first={label:'请选择'}
                this.List.unshift(first)
                
            }else{
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        },err=>{
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }
    handleChange(e){
        this.index=e.index
    }

}