import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Message } from 'primeng/primeng';
import * as _ from 'lodash';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { ZoneInfoBean, nextBean } from './bean/sunject-of-cooperation.bean';
import { CusOperationHttpService } from 'app/pages/tzb/custom/http/custom-operation/custom-operation.http.service';
import { Router } from '@angular/router';
import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { ProductManageHttpService } from '../../../../product-manage/http/product-manage.http.service';
import { coopProjectSet } from 'app/pages/tzb/customCenter/project-main-manage/project-main-manage-add/project-protocol-query/bean/project-protocol-query.bean';
@Component({
    selector: "sunject-of-cooperation-change",
    templateUrl: './sunject-of-cooperation-change.component.html',
    styleUrls: ['./sunject-of-cooperation-change.component.scss'],
    providers: [ProductManageHttpService, Commfunc, CusOperationHttpService,]

})

export class SunjectOfCooperationChange implements OnInit {
    @Input() elementGroupId: any;
    @Input() type: any;

    outVal: any;
    dealPerShow: any;
    typeIndex: any;
    NextBean: nextBean = new nextBean()
    fileList: any = [];//上传文件
    startDate: Date = null;
    endDate: Date = null;
    ch: any = CALENDAR_CN;
    detailDisplay: boolean;
    modalState: string;
    orgId: any;
    msgs: any;
    productId: any;
    newList: object = {};
    proName: any;
    zoneInfoBean: ZoneInfoBean = new ZoneInfoBean();//四级区划信息查询
    list1: any = [];
    list: any[] = [];
    list2: any = [];
    code: any;
    // 四级
    regionSHeng: any = [];
    regionCity: any = [];
    regionArea: any = [];
    regionStreet: any = [];
    regionSHeng1: any = '';//一级
    regionCity1: any = '';//二级
    regionArea1: any = '';//三级
    regionStreet1: any = '';//四级
    cengJi_2: boolean = false;
    cengJi_3: boolean = false;
    cengJi_4: boolean = false;
    parentZoneInfoTempArr: any = [];
    regionArrTemp: any = {};//四级区划
    regionArr: any = ['', '', '', ''];//四级区划
    parentZoneInfoTempNum: any = 4;//四级区划--查询--解析--标志
    queryZoneInfoTempNum: any = 0;//四级区划--查询--信息--标志


    //
    bean: coopProjectSet = new coopProjectSet();

    totleList: any[];
    constructor(
        public productPlatformHttpService: ProductManageHttpService,
        public httpService: CusOperationHttpService,
        public router: Router,
        public http: ProjectHttpService,
        public commfunc: Commfunc,
        public httpWork: CustomeHttpService,
    ) {

    }
    ngOnInit() {

        // 四级区划
        this.queryZoneInfo(0);//四级区划
        //四级区划 
        setTimeout(() => {
            this.getList();
        });
        this.codeValues();

    }

    //获取组合列表
    getList() {
        let params = {
            elementGroupId: this.elementGroupId
        }
        // let params={
        //     elementGroupId:'G0006'
        // }
        this.http.projectElementGroupDetailQuery(params).subscribe(data => {
            if (data['returnCode']['code'] == 'success') {
                for (let i = 0; i < data['elementsList'].length; i++) {
                    if (data['elementsList'][i]['listElement'] != []) {
                        for (let j = 0; j < data['elementsList'][i]['listElement'].length; j++) {
                            data['elementsList'][i]['listElement'][j].label = data['elementsList'][i]['listElement'][j]['optionValue'];
                            data['elementsList'][i]['listElement'][j].value = data['elementsList'][i]['listElement'][j]['optionKey']
                        }
                        let fir = { label: '请选择' };
                        data['elementsList'][i]['listElement'].unshift(fir);
                        data['elementsList'][i]['elementValue'] = '';
                    }
                }
                let arr = data['elementsList']
                this.list1 = arr
                console.log(this.list1)
            }
        })


    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret
    }
    queryZoneInfo(i) {
        // 四级区划信息查询
        this.queryZoneInfoTempNum = i;//中间量
        this.httpService.queryZoneInfo(this.zoneInfoBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.zoneInfoBean.zoneList = data.zoneList;
                // 初始化
                let temArr = [{ label: '请选择', value: '' }];
                for (let i in this.zoneInfoBean.zoneList) {
                    temArr.push({ label: this.zoneInfoBean.zoneList[i].zoneName, value: this.zoneInfoBean.zoneList[i].zoneId });
                }
                if (this.queryZoneInfoTempNum == 0) {
                    this.regionSHeng = temArr;
                    this.cengJi_2 = false;
                    this.cengJi_3 = false;
                    this.cengJi_4 = false;

                    this.regionSHeng1 = ''
                    this.regionCity1 = '';
                    this.regionArea1 = '';
                    this.regionStreet1 = '';
                }
                if (this.queryZoneInfoTempNum == 1) {
                    this.regionCity = temArr
                    this.cengJi_2 = false;
                    this.cengJi_3 = false;
                    this.cengJi_4 = false;

                    this.regionCity1 = '';
                    this.regionArea1 = '';
                    this.regionStreet1 = '';

                    temArr.length == 1 ? '' : this.cengJi_2 = true;
                }
                if (this.queryZoneInfoTempNum == 2) {
                    this.cengJi_3 = false;
                    this.cengJi_4 = false;

                    this.regionArea1 = '';
                    this.regionStreet1 = '';

                    this.regionArea = temArr;
                    temArr.length == 1 ? '' : this.cengJi_3 = true;
                }
                if (this.queryZoneInfoTempNum == 3) {
                    this.cengJi_4 = false;

                    this.regionStreet1 = '';

                    this.regionStreet = temArr;
                    temArr.length == 1 ? '' : this.cengJi_4 = true;
                }
            } else {
                this.msgs = [];
                data.returnCode.code ? data.returnCode.code : '查询四级区划信息失败';
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        });
    }
    choose(event, i, a, index) {
        this.regionArr[index] = a.label;
        // 四级区划再选择
        this.zoneInfoBean.parentZoneId = event.value;
        this.queryZoneInfo(i);
    }
    treeCall(e) {
        console.log(e)
        this.list1[this.typeIndex]['elementValue'] = e.orgId;
        this.list1[this.typeIndex]['elementDesc'] = e.orgName;
        this.detailDisplay = false;
        this.typeIndex = null;
    }
    // 人员
    choicesaleUser(e) {
        console.log(e)
        this.detailDisplay = false;
        if (this.list1[this.typeIndex]['frontElementCheckServe'] != null) {
            let st = this.list1[this.typeIndex]['frontElementCheckServe'];
            let arr = st.split(',')[3].split('^')
            for (let i = 0; i < arr.length; i++) {
                if (arr[i].match('-')) {
                    let dd
                    dd = arr[i].split('-');
                    for (let j = 0; j < this.list1.length; j++) {
                        if (this.list1[j]['elementEnName'] == dd[1]) {
                            this.list1[j]['elementValue'] = e[0][dd[0]];
                            console.log(this.list1[j])
                        }
                    }
                    console.log(dd)
                } else {
                    this.list1.forEach(el => {
                        if (el['elementEnName'] == arr[i]) {
                            el['elementValue'] = e[0][arr[i]];
                            console.log(el)
                        }
                    });
                }
            }
        } else {
            this.list1[this.typeIndex]['elementValue'] = e[0]['tellerId']
        }

    }
    //模态框弹出
    perTreeShow(t, i) {
        this.modalState = t;
        this.typeIndex = i;
        this.detailDisplay = true;
    }
    //数据处理
    process() {
        this.totleList = [];

        let newListo = _.clone(this.list1)
        for (let i = 0; i < newListo.length; i++) {
            if (Array.isArray(newListo[i]['elementValue']) && newListo[i]['elementValue'].length != 0) {
                let id = newListo.splice(i, 1);
                let name = newListo.splice(i, 1);

                for (let j = 0; j < id[0]['elementValue'].length; j++) {
                    let addJson = {};
                    let addJOO = {};
                    addJson['fatherElementEnName'] = id[0]['fatherElementEnName'];
                    addJson['rowNum'] = j + 1;
                    addJson['elementNum'] = id[0]['seqNum'];
                    addJson['elementGroupFlag'] = 0;
                    addJson['elementEnName'] = id[0]['elementEnName'];
                    addJson['elementName'] = id[0]['enementCnName'];
                    addJson['elementType'] = id[0]['elementType'];
                    addJson['elementValue'] = id[0]['elementValue'][j]['id'];
                    addJson['elementDesc'] = id[0]['elementValue'][j]['value'];

                    addJOO['fatherElementEnName'] = name[0]['fatherElementEnName'];
                    addJOO['rowNum'] = j + 1;
                    addJOO['elementNum'] = name[0]['seqNum'];
                    addJOO['elementGroupFlag'] = 0;
                    addJOO['elementEnName'] = name[0]['elementEnName'];
                    addJOO['elementName'] = name[0]['enementCnName'];
                    addJOO['elementType'] = name[0]['elementType'];
                    addJOO['elementValue'] = id[0]['elementValue'][j]['value'];
                    addJOO['elementDesc'] = id[0]['elementValue'][j]['id'];
                    this.totleList.push(addJson)
                    this.totleList.push(addJOO)
                }
                i--;
            } else {
                let addjson = {};
                addjson['fatherElementEnName'] = newListo[i]['fatherElementEnName'];
                addjson['rowNum'] = 1;;
                addjson['elementNum'] = newListo[i]['seqNum'];;
                addjson['elementGroupFlag'] = newListo[i]['elementGroupFlag'];;
                addjson['elementEnName'] = newListo[i]['elementEnName'];
                addjson['elementName'] = newListo[i]['enementCnName'];
                addjson['elementType'] = newListo[i]['elementType'];
                addjson['elementValue'] = newListo[i]['elementValue'];
                addjson['elementDesc'] = newListo[i]['elementDesc'];
                this.totleList.push(addjson)
            }

        }
        // console.log(totleList)
    }
    //提交
    sumbit() {
        this.msgs = [];
        this.process();
        this.bean.elementGroupList = this.totleList;
        console.log(this.totleList)
        this.bean.operateType = '1';
        this.bean.elementGroupId = this.elementGroupId;
        // this.bean.elementGroupId='G0006';
        this.http.coopProjectSet(this.bean).subscribe(data => {
            if (data['returnCode']['returnCode'] == 'success') {
                this.nextDom();
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });

            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });

            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
        // this.router.navigate(['/pages/tzb/custom-center/project-main-manage/project-main-manage-add'], { queryParams: { state: '2' } })
    }
    save() {
        // this.router.navigate(['/pages/tzb/custom-center/project-main-manage/project-main-manage-add'], { queryParams: { state: '2' } })
    }
    //手动输入日期时，失去焦点时进行判断
    onchange1() {
        this.showDataStart()
    }
    onchange2() {
        this.showDataEnd()
    }
    //开始日期
    showDataStart() {
        if (this.startDate && this.endDate) {
            if (this.startDate > this.endDate) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
                setTimeout(() => {
                    this.startDate = null;
                })
            }
        }
    }
    //结束日期
    showDataEnd() {
        if (this.startDate && this.endDate) {
            if (this.startDate > this.endDate) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
                setTimeout(() => {
                    this.endDate = null;
                })
            }
        }
    }
    // 产品编号
    assoctate(e) {
        console.log(e)
        //定义描述
        let string = ""
        let arr = [];
        for (let i = 0; i < e.length; i++) {
            string += e[i].productName + ',';
            let json = {};
            json['value'] = e[i]['productName'];
            json['id'] = e[i]['productId']
            arr.push(json)
        }
        //定义输入字段
        this.list1[this.typeIndex]['elementValue'] = arr;
        this.list1[this.typeIndex]['elementDesc'] = string;
        console.log(this.list1[this.typeIndex])
        this.detailDisplay = false;
    }
    //上传文件
    myUploader(event) {
        console.log(event)
        if (event.xhr.response) { //上传成功 后台返回的 文件路径 和文件名
            this.bean.protocolContent = JSON.parse(event.xhr.response);
            this.fileList = this.bean.protocolContent;
        }
        // console.log(this.coopProjectFeasibilityPlanSetBean.planContent)
        if (this.fileList.length == 1) {
            this.bean.protocolContent = this.fileList[0]['fileUrl'] + this.fileList[0]['fileName']
        }
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '上传成功！' });
    }
    //工作流走下一步选人
    nextDom() {
        let params = {
            userIdMap: { userId: this.NextBean.userId },
            proxessDesc: '可行性方案创建',
            taskId: this.NextBean.taskId,
            bussinessMap: {

            }
        }
        this.httpWork.bpmFindNextNodeInfo(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dealPerShow = true;
                this.outVal = data['result']['nextOutData'][0]['selectUserList'];

            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }
    outDisplayCall(e) {
        this.dealPerShow = e
    }
    outValueCall(e) {
        let persons = '';
        e.forEach(el => {
            persons += el['tellerId'] + ','
        });
        let params = {
            userId: this.NextBean.userId,
            taskId: this.NextBean.taskId,
            bussinessMap: {
                specifyNextOpers: persons,
                dataMap: {

                }
            }
        }
        this.httpWork.bpmNextTask(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }

}