import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { TZB_HTTP_XIN } from 'app/@uisftech/common/service/http.constant';
import { Router } from '@angular/router';
import { SupplyChainHttpService } from '../../http/supply-chain.http.service';
@Component({
    selector: 'orderInfo-table-detail',
    templateUrl: './orderInfo-table-detail.component.html',
    styleUrls: ['./orderInfo-table-detail.component.scss'],
    providers: [Commfunc,SupplyChainHttpService]
})
/**客户信息表 */
export class OrderomInfoTableDetail implements OnInit {
    @Input() inValue: any;
    msgs: Message[] = []; //提示信息
    dataList: any ={};
    code: any;
    SignStatus: any = [];//签约状态
    ch: any = CALENDAR_CN;
    CorporationCode: any = [];
    first: number = 0;//分页控制
    totalNum: number;
    signDate: Date = null;
    recommendDate: Date = null;
    OrderStatus: any = [];//证件类型
    projectId: string;
    custId: string;
    constructor(
        public projectHttpService: SupplyChainHttpService,
        public commfunc: Commfunc,

    ) {
        this.codeValues() //调用方法，获取全部码值
        this.OrderStatus = this.code['ORDERSTATUS']; //证件类型

    }

    ngOnInit() {
         
        let param = {
            projectId: this.inValue.projectId,
            innerOrderId: this.inValue.innerOrderId,
        }
        this.query(param)

    }
    //查询
    query(item) {
        this.projectHttpService.supplyChainOrderInfo(item).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.dataList = data.resultMap;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }



}
