import { Component, OnInit, Output, Input, OnChanges, EventEmitter } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message, ConfirmationService, ConfirmDialogModule, } from 'primeng/primeng';
import * as _ from 'lodash';

import { ProjectHttpService } from '../../../http/project-main-manage.service';

import { Router, ActivatedRoute } from '@angular/router';
import { applyApproalCoreEnterprisesAdd, coreEnterprisesInfListQuery, coreEnterprisesInfoDetail } from '../bean/core-customers.bean';
import { applyApproalCoreEnterprisesModify, coreEnterprisesInfDelete } from '../bean/core-customers.bean';
import { DetailBean11, DetailBean12, DetailBean13, DetailBean14 } from '../bean/essential-data';
import { DetailBean21, DetailBean22, DetailBean23, DetailBean24, DetailBean25 } from '../bean/certificate-data';
import { DetailBean31, DetailBean32 } from '../bean/relevant-data';
import { CusViewHttpService } from '../../../../../custom/http/custom-view/custom-view.http.service';
import { supplyChainPlatformList } from '../../three-platform/bean/three-platform.bean';
import { checkEnAndNum, checkCard } from 'app/@uisftech/common/service/validator';
import { CALENDAR_CN } from "app/@uisftech/common/constant/config";
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
@Component({
    selector: "core-customers-add-modify",
    templateUrl: './core-customers-add-modify.component.html',
    styleUrls: ['./core-customers-add-modify.component.scss'],
    providers: [CusViewHttpService, Commfunc, ConfirmationService]
})

export class CoreCustomersAddModify implements OnInit {
    mainFlowNo: any;
    typekey: any = '0';
    outVal: any;
    dealPerShow: boolean;
    branchId: any;
    user: any;
    organizationType: any;
    taxRegistrationType: any;
    @Input() inValue: any;
    //新增的联系人参数
    addValue: any = [];

    platformName: any;
    type: any;
    tableData: any[] = [];
    relationEntrustIdType: any;
    relationLegalIdType: any;
    licenceType: any;
    GENDER: any;
    businessType: any;
    numb: any;
    msgs: any;

    contactsdis: boolean;
    display: boolean;
    //数据bean
    detailBean: coreEnterprisesInfoDetail = new coreEnterprisesInfoDetail();
    //码值
    code: any;
    ch: any = CALENDAR_CN;

    DetailBeanCatch1: any = [];
    DetailBeanCatch2: any = [];
    DetailBeanCatch3: any = [];
    DetailBeanCatch4: any = [];
    DetailBeanCatch5: any = [];
    DetailBeanCatch6: any = [];
    DetailBeanCatch7: any = [];
    DetailBeanCatch8: any = [];
    DetailBeanCatch9: any = [];
    DetailBeanCatch10: any = [];
    // DetailBeanCatch11: any = [];

    platformIdentify: string;
    Platformbean: supplyChainPlatformList = new supplyChainPlatformList();
    platformIdentifyList: any;

    provinceOptionsBasicPro: any[];//基础信息省
    cityOptionsBasicPro: any[];//基础信息市
    zoneOptionsBasicPro: any[];//基础信息区
    province: any;//省
    city: any;//市
    county: any;//区

    tableHead: any[] = [
        {
            label: '姓名',
            value: 'contactsName',
            wth: '150px'
        },
        {
            label: '手机',
            value: 'phone',
            wth: '150px'
        },
        {
            label: '邮箱',
            value: 'email',
            wth: '150px'
        }
    ];

    constructor(
        public httpService: ProjectHttpService,
        public commfunc: Commfunc,
        public activatedRoute: ActivatedRoute,
        public router: Router,
        public CusViewHttpService: CusViewHttpService,
        public confirmationService: ConfirmationService,
        public httpWork: CustomeHttpService,
    ) {
        this.activatedRoute.queryParams.subscribe(queryParams => {

            if (JSON.stringify(queryParams) == '{}') {
                let detprm = JSON.parse(this.activatedRoute['snapshot']['params']['dataMap'])
                console.log(this.activatedRoute['snapshot']['params'])
                this.typekey = '1';
                this.type = '3';
                this.detailBean.coreEnterprisesNo = detprm['coreEnterprisesNo'];
                if (this.activatedRoute.snapshot.params.mainFlowNo) {
                    this.mainFlowNo = this.activatedRoute.snapshot.params.mainFlowNo;
                }
            } else {
                this.type = queryParams.type;//1为新增2为修改
            }

            if (this.type == '2') {
                this.detailBean.coreEnterprisesNo = queryParams.coreEnterprisesNo;
            }

            if (this.type == '2' || this.type == '3') {
                this.QueryModify();
                setTimeout(() => {
                    this.changeplatform();
                    // 省市区选择初始化
                    let zh = JSON.parse(this.commfunc.getSessionDataCH('productionOpenInfo'));
                    if (zh == undefined || zh == null) {
                        let ap = '';
                        this.queryZoneInfoModify(ap);
                    }
                }, 500);
            }
            else {
                this.foefor();
            }
        })

        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.userId) {
            this.user = s.userId
        }
        if (s.orgId) {
            this.branchId = s.orgId
        }

    }
    ngOnInit() {
        if (this.type != '3') {
            let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (s['globalSeqNo'] && this.typekey != '1') {
                delete s['globalSeqNo'];
                this.commfunc.setSessionData('commonHeader', JSON.stringify(s));
            }
        }

        this.DetailBeanCatch1 = DetailBean11;//企业基本信息表头
        this.DetailBeanCatch2 = DetailBean12;//企业基本信息栏
        this.DetailBeanCatch3 = DetailBean13;//银行卡信息栏
        // this.DetailBeanCatch11 = DetailBean14;//归属信息栏
        this.DetailBeanCatch4 = DetailBean21;//营业执照信息栏
        this.DetailBeanCatch5 = DetailBean22;//税务登记信息栏
        this.DetailBeanCatch6 = DetailBean23;//组织机构代码信息栏
        this.DetailBeanCatch7 = DetailBean24;//开户许可证信息栏
        this.DetailBeanCatch8 = DetailBean25;//机构信用代码信息栏
        this.DetailBeanCatch9 = DetailBean31;//法人证件信息栏
        this.DetailBeanCatch10 = DetailBean32;//委托人信息栏
        // 查询平台识别码列表
        this.queryIdentifyList();

        // 省市区选择初始化
        let param = JSON.parse(this.commfunc.getSessionDataCH('productionOpenInfo'));
        if (param == undefined || param == null) {
            let ap = '';
            this.queryZoneInfo(ap);
        }

        this.codeValues();
        this.licenceType = this.code['CERT_TYPE_CORPORATE'];//企业证件类型
        this.relationLegalIdType = this.code['CERT_TYPE_PERSON'];//法人证件类型
        this.relationEntrustIdType = this.code['CERT_TYPE_PERSON'];//委托人证件类型
        this.GENDER = this.code['GENDER']; //性别
        this.businessType = this.code['CERT_TYPE_CORPORATE'];//企业一业执照信息类型
        this.taxRegistrationType = this.code['CERT_TYPE_CORPORATE'];//登记注册类型
        this.organizationType = this.code['CERT_TYPE_CORPORATE'];//机构类型
    }
    handleChange(e) {
        this.numb = e.index;
    }

    //初始化新增页面执行清空历史赋值
    foefor() {
        let ss = [DetailBean11, DetailBean12, DetailBean13, DetailBean21, DetailBean22, DetailBean23, DetailBean24, DetailBean25, DetailBean31, DetailBean32];
        for (let i = 0; i < ss.length; i++) {
            ss[i].forEach(e => {
                e.ENname1 = '';
            });
        }
    }

    // 查询数据·修改
    QueryModify() {
        let param = {
            coreEnterprisesNo: this.detailBean.coreEnterprisesNo,
        }
        this.httpService.coreEnterprisesInfoDetail(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                let ss = [DetailBean11, DetailBean12, DetailBean13, DetailBean21, DetailBean22, DetailBean23, DetailBean24, DetailBean25, DetailBean31, DetailBean32];
                for (let i = 0; i < ss.length; i++) {
                    ss[i].forEach(e => {
                        e.ENname1 = data[e.ENname];
                    });
                }
                this.tableData = data['contacts'] ? data['contacts'] : [];
                this.detailBean.licenceType = data.licenceType;
                this.detailBean.relationLegalIdType = data.relationLegalIdType;
                this.detailBean.relationEntrustIdType = data.relationEntrustIdType;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
    }

    // 数据赋值提交
    combinePara() {
        let ret = {};
        let data = [DetailBean11, DetailBean12, DetailBean13, DetailBean21, DetailBean22, DetailBean23, DetailBean24, DetailBean25, DetailBean31, DetailBean32];
        for (let i = 0; i < data.length; i++) {
            data[i].forEach(e => {
                if (e.ENname == 'businessCapital') {
                    ret[e.ENname] = Number(e.ENname1)
                } else {
                    ret[e.ENname] = e.ENname1;
                }
                if (typeof e.ENname1 != 'string' && e['inputtype'] && e['inputtype'] == 'time' && e.ENname1 != '' && e.ENname1 != null) {
                    ret[e.ENname] = this.commfunc.transDateN(e.ENname1)
                } else {
                    ret[e.ENname] = e.ENname1;
                }

            });
        }
        ret['licenceType'] = this.detailBean.licenceType;
        ret['relationLegalIdType'] = this.detailBean.relationLegalIdType;
        ret['relationEntrustIdType'] = this.detailBean.relationEntrustIdType;
        ret['contacts'] = this.tableData;
        ret['userId'] = this.user;
        ret['branchId'] = this.branchId;
        if (this.type == '2' || this.type == '3') {
            ret['operateType'] = '3';
            ret['coreEnterprisesNo'] = this.detailBean.coreEnterprisesNo;
        } else {
            ret['operateType'] = '1';
        }

        return ret;
    }

    // 保存·修改
    changesave() {
        let para = this.combinePara();
        console.log(para['platformIdentify'])
        if (para['platformIdentify'] == '' && para['platformName'] == '' || para['platformIdentify'] == null) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "平台识别码未填入" });
            return;
        } else if (para['platformName'] == '') {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "平台名称未成功填入" });
            return;
        } else if (para['idOfCoreByBank'] == '' && para['custName'] == '' || para['idOfCoreByBank'] == null) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "核心企业客户号未填入" });
            return;
        } else if (para['custName'] == '') {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "核心企业名称未成功填入" });
            return;
        } else if ((para['creditCodeOfCore'] == '' && para['licenseOfCore'] == '') || (para['creditCodeOfCore'] != '' && para['licenseOfCore'] != '')) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "统一社会信用代码与登记注册号选填其一" });
            return;
        }
        this.httpService.applyApproalCoreEnterprises(para).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                this.goback();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }

            let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (s.extParam.specifyNextOpers || s.extParam.specifyNextNode) {
                delete s.extParam.specifyNextOpers;
                delete s.extParam.specifyNextNode;
                let commonHeader = s;
                this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
            }

        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
    }

    // 查询平台下拉
    queryIdentifyList() {
        this.Platformbean.pageSize = 2000;
        this.Platformbean.pageNum = 1;
        this.Platformbean.status = '1';
        this.httpService.supplyChainPlatformList(this.Platformbean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.platformIdentifyList = data['returnList'];
                this.platformIdentifyList.forEach(t => {
                    t.label = t.platformIdentify;
                    t.value = t.platformIdentify;
                })
                let first = { label: '请选择' }
                this.platformIdentifyList.unshift(first);
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }
    // 赋值到平台名称
    changeplatform() {
        this.DetailBeanCatch1.forEach(e => {
            if (e.inputtype && e.ENname == 'platformIdentify') {
                this.platformIdentify = e.ENname1
            }
            if (e.inputtype && e.inputtype == 'platformName') {
                this.platformIdentifyList.forEach(f => {
                    if (f.platformIdentify == this.platformIdentify) {
                        e.ENname1 = f.platformName
                    }
                })
            }
        })
    }

    //所在行政区划
    queryZoneInfo(data) {
        if (data == '') {
            let param_zone = {
                parentZoneId: ''
            }
            //四级区划查询省
            this.CusViewHttpService.queryZoneInfo(param_zone).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.provinceOptionsBasicPro = this.commfunc.listToLabelvalue(data.zoneList, 'zoneName', 'zoneId');
                    this.provinceOptionsBasicPro.unshift({ label: '请选择', value: '' });
                    this.cityOptionsBasicPro = [{ label: '请选择', value: '' }];
                    this.zoneOptionsBasicPro = [{ label: '请选择', value: '' }];
                    this.city = '';
                    this.county = '';
                }
            });
        } else if (data == '1') {
            if (this.province == '') {
                this.cityOptionsBasicPro = [{ label: '请选择', value: '' }];
                this.zoneOptionsBasicPro = [{ label: '请选择', value: '' }];
                this.city = '';
                this.county = '';
            } else {
                let param_zone = {
                    parentZoneId: this.province
                }
                //四级区划查询市
                this.CusViewHttpService.queryZoneInfo(param_zone).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.cityOptionsBasicPro = this.commfunc.listToLabelvalue(data.zoneList, 'zoneName', 'zoneId');
                        this.cityOptionsBasicPro.unshift({ label: '请选择', value: '' });
                        this.zoneOptionsBasicPro = [{ label: '请选择', value: '' }];
                        this.county = '';
                    }
                });
            }
        } else if (data == '2') {
            if (this.city == '') {
                this.zoneOptionsBasicPro = [{ label: '请选择', value: '' }];
                this.county = '';
            } else {
                let param_zone = {
                    parentZoneId: this.city
                }
                //四级区划查询区
                this.CusViewHttpService.queryZoneInfo(param_zone).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.zoneOptionsBasicPro = this.commfunc.listToLabelvalue(data.zoneList, 'zoneName', 'zoneId');
                        this.zoneOptionsBasicPro.unshift({ label: '请选择', value: '' });
                    }
                });
            }
        }
        // 转化赋值到DetailBean
        this.DetailBeanCatch2.forEach(e => {
            if (e.inputtype && e.inputtype == 'p') {
                e.ENname1 = this.province
            }
            if (e.inputtype && e.inputtype == 'c') {
                e.ENname1 = this.city
            }
            if (e.inputtype && e.inputtype == 'z') {
                e.ENname1 = this.county
            }
        });
    }

    // 选择客户号
    perTreeShow() {
        this.display = true;
    }
    // 传递选择客户数据
    getMsg(f) {
        this.DetailBeanCatch1.forEach(e => {
            if (e.ENname == 'idOfCoreByBank') {
                e.ENname1 = f['custNo']
            }
            if (e.ENname == 'custName') {
                e.ENname1 = f['custName']
            }
        })
        this.DetailBeanCatch2.forEach(e => {
            if (e.ENname == 'custName') {
                e.ENname1 = f['custName']
            }
        })
        this.display = false;
    }
    //联系人新增
    peopleAdd() {
        this.contactsdis = true;
        this.addValue = {
            type: '1',
        };
    }


    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    goback() {
        if (this.typekey == '1') {
            window.history.go(-1);
        } else {
            this.router.navigate(['/pages/tzb/custom-center/project-main-manage/core-customers'])
        }
    }

    // 修改查询--赋值到平台名称
    changeplatformModify() {
        this.DetailBeanCatch1.forEach(e => {
            if (e.inputtype && e.ENname == 'platformIdentify') {
                this.platformIdentify = e.ENname1
            }
            if (e.inputtype && e.inputtype == 'platformName') {
                this.platformIdentifyList.forEach(f => {
                    if (f.platformIdentify == this.platformIdentify) {
                        e.ENname1 = f.platformName
                    }
                })
            }
        })
    }

    // 修改查询--所在行政区划
    queryZoneInfoModify(data) {
        let ssq;
        // 转化赋值到DetailBean
        this.DetailBeanCatch2.forEach(e => {
            if (e.inputtype && e.inputtype == 'p') {
                this.province = e.ENname1
            }
            if (e.inputtype && e.inputtype == 'c') {
                this.city = e.ENname1
            }
            if (e.inputtype && e.inputtype == 'z') {
                this.county = e.ENname1
            }
        });

        if (data == '') {
            let param_zone = {
                parentZoneId: ''
            }
            //四级区划查询省
            this.CusViewHttpService.queryZoneInfo(param_zone).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.provinceOptionsBasicPro = this.commfunc.listToLabelvalue(data.zoneList, 'zoneName', 'zoneId');
                    this.provinceOptionsBasicPro.unshift({ label: '请选择', value: '' });
                    this.cityOptionsBasicPro = [{ label: '请选择', value: '' }];
                    this.zoneOptionsBasicPro = [{ label: '请选择', value: '' }];
                    ssq = '1';
                }
                if (ssq == '1') {
                    let param_zone = {
                        parentZoneId: this.province
                    }
                    //四级区划查询市
                    this.CusViewHttpService.queryZoneInfo(param_zone).subscribe(data => {
                        if (data.returnCode.code == 'success') {
                            this.cityOptionsBasicPro = this.commfunc.listToLabelvalue(data.zoneList, 'zoneName', 'zoneId');
                            this.cityOptionsBasicPro.unshift({ label: '请选择', value: '' });
                            this.zoneOptionsBasicPro = [{ label: '请选择', value: '' }];
                            ssq = '2'
                        }
                        if (ssq == '2') {
                            let param_zone = {
                                parentZoneId: this.city
                            }
                            //四级区划查询区
                            this.CusViewHttpService.queryZoneInfo(param_zone).subscribe(data => {
                                if (data.returnCode.code == 'success') {
                                    this.zoneOptionsBasicPro = this.commfunc.listToLabelvalue(data.zoneList, 'zoneName', 'zoneId');
                                    this.zoneOptionsBasicPro.unshift({ label: '请选择', value: '' });
                                }
                            })
                        }
                    })
                }
            })
        }
    }

    // 身份证校验
    onCheck(event) {
        if (event.target.value != '') {
            if (checkCard(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入正确的身份证号' }];
                event.target.value = '';
            }
        }
    }

    //添加后返回的值
    addCall(param) {
        this.contactsdis = param['display'];
        if (param.peoplelist) {
            this.tableData = _.clone(param.peoplelist);
        }


    }
    //联系人删除
    delete(i, e) {
        this.tableData.splice(i, 1);
        this.tableData = _.clone(this.tableData);
    }
    // 联系人修改
    update(i, item) {
        this.contactsdis = true;
        this.addValue = {
            item: item,
            type: '2',
        };
    }

    // 审批打回提交
    submit() {
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (this.typekey == '1') {
            s['globalSeqNo'] = this.mainFlowNo;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(s));
        }
        setTimeout(() => {
            this.nextDom()
        }, 800);       
    }
    // 审批选人
    nextDom() {
        this.msgs = [];
        let params = {
            userIdMap: {
                userId: JSON.parse(this.commfunc.getSessionData('commonHeader')).userId,
                userPost: JSON.parse(this.commfunc.getSessionData('commonHeader')).extParam.postId,
            },
            proxessDesc: '选人策略',
            processCode: 'GYLHXQYSP',
            taskId: this.activatedRoute.snapshot.params.taskId,
            bussinessMap: {
            }
        }
        this.httpWork.bpmFindNextNodeInfo(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.result.nextOutData && data.result.nextOutData.length >= 1 && data.result.nextOutData[0].selectUserList && data.result.nextOutData[0].selectUserList.length >= 1) {
                    this.dealPerShow = true;
                    this.outVal = data['result']['nextOutData'];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                } else {
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '所要选择人员没有配置' });
                }
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }
    //选人下一步
    outValueCall(e) {
        let persons = '';
        let specifyNextNode = '';
        e.forEach(el => {
            persons += el['tellerId'] + ',';
            specifyNextNode = el.outFlowId;
        });
        if (this.typekey == '1') {
            let params = {
                userId: JSON.parse(this.commfunc.getSessionData('commonHeader')).userId,
                taskId: this.activatedRoute.snapshot.params.taskId,
                bussinessMap: {
                    specifyNextOpers: persons,
                    specifyNextNode: specifyNextNode,
                    dataMap: {
                        coreEnterprisesNo: this.detailBean.coreEnterprisesNo,
                    },
                }
            }
            this.httpWork.bpmNextTask(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.changesave();
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                    setTimeout(() => {
                        this.goback()
                    }, 500);
                } else {
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
            })
        } 
        // else {
        //     this.changesave();
        // }
    }
    outDisplayCall(e) {
        this.dealPerShow = e;
    }

}