import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import { ActivatedRoute } from '@angular/router';
import { CoopProjectFeasibilityPlanDetailQueryBean } from 'app/pages/tzb/customCenter/project-main-manage/project-main-manage-add/feasibility-query/bean/feasibility-query.bean';
import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';
import { TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { CoopProjectTypeListQueryBean } from 'app/pages/tzb/customCenter/project-main-manage/project-main-manage-search/bean/project-main-manage-search.bean';
import { Router } from '@angular/router';
@Component({
    selector: "programme-details",
    templateUrl: './programme-details.component.html',
    styleUrls: ['./programme-details.component.scss'],
})

export class ProgrammeDetails implements OnInit, OnChanges {
    details2: any;
    mainFlowNo: any;
    coopProjectFeasibilityPlanDetailQueryBean: CoopProjectFeasibilityPlanDetailQueryBean = new CoopProjectFeasibilityPlanDetailQueryBean();
    coopProjectTypeListQueryBean: CoopProjectTypeListQueryBean = new CoopProjectTypeListQueryBean();
    textType: any = '02';
    details: any = {};
    details1: any = {};
    msgs: any;
    Type: any;
    code: any;
    type1: any;
    dataMap: any;
    display: boolean;
    coopName: any;
    coopplangName: any;
    constructor(
        public router: Router,
        private activatedRoute: ActivatedRoute,
        private http: ProjectHttpService,
        private commfunc: Commfunc,
    ) {

    }
    ngOnChanges() {

    }
    goback() {
        //window.history.go(-1)
        this.router.navigate(['/pages/tzb/custom-center/project-main-manage/Feasibilityquery'], { queryParams: { index: null } })

    }
    ngOnInit() {
        // 获取码值
        this.codeValues();
        this.Type = this.code['planType'];

        this.activatedRoute.queryParams.subscribe(queryParams => {
            debugger;
            if (this.activatedRoute.snapshot.params.from == 'reminder-do') {
                let t = this.commfunc.getSessionDataCH('programme-details-from-reminder-do');
                t = JSON.parse(t);
                this.details2 = JSON.parse(t['dataMap']);
                this.coopProjectTypeListQueryBean.coopProjectTypeId = this.details2['coopProjectTypeId'];
                this.queryleixing();
                this.coopProjectFeasibilityPlanDetailQueryBean.planId = this.details2['planId'];
                this.details1.bigClassName = this.activatedRoute.snapshot.params.bigClassName;
                this.details1.littleClassName = this.activatedRoute.snapshot.params.littleClassName;
                this.details1.workCreateName = this.activatedRoute.snapshot.params.workCreateName;
                this.details1.createDate = this.activatedRoute.snapshot.params.createDate;
                this.type1 = '1';
                if (this.activatedRoute.snapshot.params.mainFlowNo) {
                    this.mainFlowNo = this.activatedRoute.snapshot.params.mainFlowNo;
                }
            } else {
                this.coopProjectFeasibilityPlanDetailQueryBean.planId = queryParams['planId'];
                this.coopProjectTypeListQueryBean.coopProjectTypeId = queryParams['coopProjectTypeId'];
                this.queryleixing();
            }

        })
        this.getMsgs();
    }
    queryleixing() {
        let param = {
            coopProjectTypeId: this.coopProjectTypeListQueryBean.coopProjectTypeId
        }
        this.http.coopProjectTypeDetailQuery(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.coopName = data['coopProjectTypeName']
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }
    getMsgs() {
        this.msgs = [];
        let param = {
            planId: this.coopProjectFeasibilityPlanDetailQueryBean.planId
        }
        this.http.coopProjectFeasibilityPlanDetailQuery(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.details = data;
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }
    perTreeShow() {
        this.display = true;
    }
    //获取码值
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    click() {
        // planContent:"/copdata/20181018090320/方案，主体状态查询修改.doc"
        let arr = this.details.planContent.split('/');
        let path = '';
        for (let j = 1; j < arr.length - 1; j++) {
            path += '/' + arr[j];
        }
        path = path + '/';
        let donwname = arr[arr.length - 1];
        open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(donwname) + "&fileUrl=" + path, '_blank');
    }

}