    // 产品编号
    assoctate(e) {
        //定义描述
        let productName = "";
        let productId = "";
        let newli = {};
        for (let i = 0; i < e.length; i++) {
            productName += e[i].productName + ',';
            productId += e[i].productId + ',';
        }

        newli['productName'] = (productName.substring(productName.length - 1) == ',') ? productName.substring(0, productName.length - 1) : productName;
        newli['productId'] = (productId.substring(productId.length - 1) == ',') ? productId.substring(0, productId.length - 1) : productId;

        if (this.list1[this.typeIndex]['frontElementCheckServe'] != null && this.list1[this.typeIndex]['frontElementCheckServe'] != '') {
            let st = this.list1[this.typeIndex]['frontElementCheckServe'];
            let arr = st.split(',')[4].split('^')
            for (let i = 0; i < arr.length; i++) {
                if (arr[i].match('-')) {
                    let dd
                    dd = arr[i].split('-');
                    for (let j = 0; j < this.list1.length; j++) {
                        if (this.list1[j]['elementEnName'] == dd[1]) {
                            this.list1[j]['elementValue'] = e[0][dd[0]];
                        }
                    }

                } else {
                    this.list1.forEach(el => {
                        if (el['elementEnName'] == arr[i]) {
                            el['elementValue'] = newli[arr[i]];
                            el['elementDesc'] = newli[arr[i]];
                        }
                    });
                }
            }
        }

        this.detailDisplay = false;
    }
    //上传文件
    myUploader(event) {
        if (event.xhr.response && event.xhr.status == 200 && event.xhr.statusText == "OK") { //上传成功 后台返回的 文件路径 和文件名
            // this.bean.protocolContent = JSON.parse(event.xhr.response);
            this.fileList = JSON.parse(event.xhr.response);
            console.log(this.fileList)
            if (this.fileList.errorMsg) {//后台校验文件名称,抛错
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: this.fileList.errorMsg });
                return
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: '上传成功！' });
            }
        }

        if (this.fileList.length == 1) {
            this.bean.protocolContent = this.fileList[0]['fileUrl'] + this.fileList[0]['fileName'];
            this.fileTure = true;
            this.mmp = this.bean['protocolContent'];
        }

    }
