    //调用服务
    change(i) {
        let kkzhi = this.list1[i]['elementValue'];

        if (this.list1[i]['elementValue'] < 0 || kkzhi == null) {
            this.list1[i]['elementValue'] = '';
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入正确数据' });
        }
        if (this.list1[i]['frontElementCheckServe'] == '' || this.list1[i]['frontElementCheckServe'] == null) {
            return
        } else {
            let srt = this.list1[i]['frontElementCheckServe'].split(',');
            if (srt[0] == '0') {
                let post = srt[2];
                let url = srt[1];
                let params = {};
                let arr = post.split('^')
                for (let i = 0; i < arr.length; i++) {
                    if (arr[i].match('-')) {
                        let key = arr[i].split('-')[1];
                        let val = arr[i].split('-')[0];
                        if (this['bean'][val]) {
                            params[key] = this['bean'][val]
                        } else {
                            this.list1.forEach(e => {
                                if (e['elementEnName'] == val) {
                                    params[key] = e['elementValue']
                                }
                            })
                        }

                    } else {
                        let key = arr[i]
                        if (this['bean'][key]) {
                            params[key] = this['bean'][key]
                        } else {
                            this.list1.forEach(e => {
                                if (e['elementEnName'] == key) {
                                    params[key] = e['elementValue']
                                }
                            })
                        }
                    }
                };
                this.commer(params, url).subscribe(data => {
                    if (data['returnCode']['code'] == 'success') {
                        this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
                    } else {
                        this.list1[i]['elementValue'] = null;
                        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                    }
                }, err => {
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '服务调用失败' }];

                })
            } else if (srt[0] == '2') {
                let b = srt[1];
                let a = {};
                for (let v = 0; v < this.list1.length; v++) {
                    if (this.list1[v]['elementType'] == '8' && (typeof this.list1[v]['elementValue'] != 'string')) {
                        this.list1[v]['elementValue'] = this.commfunc.transDateN(this.list1[v]['elementValue'])
                    }
                    a[this.list1[v]['elementEnName']] = this.list1[v]['elementValue'];
                }

                let datejt = this.commfunc.transDateN(this.datenew);
                a['currDATE'] = datejt;
                if (eval(b)) {
                    this.list1[i]['elementValue'] = null;
                    this.msgs = [{ severity: 'error', summary: '提示', detail: srt[2] }];
                }

            }
            // 计算保证金
            else if (srt[0] == '3') {
                let b = srt[1];
                let a = {};
                for (let v = 0; v < this.list1.length; v++) {
                    a[this.list1[v]['elementEnName']] = this.list1[v]['elementValue'];
                }
                let c = eval(b);
                this.list1.forEach(element => {
                    if (element.elementEnName == srt[2]) {
                        element.elementValue = c.toFixed(6);
                    }
                });
            }
        }
    }

