    typedele() {
        // 回显为文件
        if (this.wordtype == '3') {
            if (this.bean['protocolContentType'] == '0') {
                this.bean['protocolContent'] = '';
                this.bean['protocolContent'] = this.ccp;
            } else if (this.bean['protocolContentType'] == '1') {
                this.bean['protocolContent'] = '';
                this.fileList = [];
            }
        }
        // 回显为文本
        else if (this.wordtype == '2') {
            if (this.bean['protocolContentType'] == '0') {
                this.bean['protocolContent'] = '';
            } else if (this.bean['protocolContentType'] == '1') {
                this.bean['protocolContent'] = '';
                this.bean['protocolContent'] = this.mmp;
            }
        }
        // 初始
        else if (this.wordtype == '0') {
            if (this.bean['protocolContentType'] == '0') {
                this.bean['protocolContent'] = '';
                this.bean['protocolContent'] = this.ccp;
            } else if (this.bean['protocolContentType'] == '1') {
                this.bean['protocolContent'] = '';
                this.bean['protocolContent'] = this.mmp;
            }
        }
    }
