import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import { Router } from '@angular/router';
import { CoopProjectFeasibilityPlanListQueryBean, CoopProjectFeasibilityPlanSetBean } from 'app/pages/tzb/customCenter/project-main-manage/project-main-manage-add-qita/feasibility-query-qita/bean/feasibility-query-qita.bean';
import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';

@Component({
    selector: "feasibility-query-qita",
    templateUrl: './feasibility-query-qita.component.html',
    styleUrls: ['./feasibility-query-qita.component.scss'],
})

export class FeasibilityqueryQita implements OnInit {
    @Input('List') List: any;
    // @Input('indx') index1: any;
    coopProjectFeasibilityPlanSetBean:CoopProjectFeasibilityPlanSetBean=new CoopProjectFeasibilityPlanSetBean();
    coopProjectFeasibilityPlanListQueryBean: CoopProjectFeasibilityPlanListQueryBean = new CoopProjectFeasibilityPlanListQueryBean();
    tableData: any[] = [];
    first: any = 0;
    total: any;
    msgs: any[];
    code:any;
    // 添加可行性方案状态查询
    subject_state:any;

    constructor(
        public router: Router,
        public http: ProjectHttpService,
        public commfunc:Commfunc,
    ) {

    }
    ngOnInit() {
        this.codeValues();
        this.coopProjectFeasibilityPlanListQueryBean.pageNum = 1;
        this.coopProjectFeasibilityPlanListQueryBean.pageSize = 10;
        this.queryList();
        //  添加可行性方案状态查询
        this.subject_state = this.code['subject_state'];
        //this.coopProjectFeasibilityPlanListQueryBean.planStatus = this.subject_state;
        // console.log(this.index1)
    }
    queryList() {
        this.msgs = []
        this.http.coopProjectFeasibilityPlanListQuery(this.coopProjectFeasibilityPlanListQueryBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data['returnList'];
                this.total = data['total']
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }
    copy(item) {
        console.log(item)
        this.router.navigate(['/pages/tzb/custom-center/project-main-manage/programme-details'],{queryParams:{planId:item.planId}})
    }
    update(item) {
        this.router.navigate(['/pages/tzb/custom-center/project-main-manage/scheme-modification'],{queryParams:{planId:item.planId}})
    }
    delete(item){
        this.coopProjectFeasibilityPlanSetBean.planId=item.planId;
        this.coopProjectFeasibilityPlanSetBean.operateType='3';
        this.http.coopProjectFeasibilityPlanSet(this.coopProjectFeasibilityPlanSetBean).subscribe(data=>{
            if(data.returnCode.code=='success'){
                this.queryList()
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            }else{
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        },err=>{
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }
    powerList(item){
        this.coopProjectFeasibilityPlanSetBean.planId=item.planId;
        this.coopProjectFeasibilityPlanSetBean.operateType='4';
        this.http.coopProjectFeasibilityPlanSet(this.coopProjectFeasibilityPlanSetBean).subscribe(data=>{
            if(data.returnCode.code=='success'){
                this.queryList()
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            }else{
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        },err=>{
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }
    //分页
    paginate(event) {
        this.coopProjectFeasibilityPlanListQueryBean.pageSize = event.rows * 1;
        //当前页
        let currentPage = event.page + 1;
        this.coopProjectFeasibilityPlanListQueryBean.pageNum = currentPage;
        this.first = event.page * event.rows;
        /**
         * --2018.1.15
         */
        //调用查询的方法
        this.queryList();
    }
      //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //重置
  clear(){
    this.coopProjectFeasibilityPlanListQueryBean = new CoopProjectFeasibilityPlanListQueryBean();
    this.coopProjectFeasibilityPlanListQueryBean.pageNum = 1;
    this.coopProjectFeasibilityPlanListQueryBean.pageSize = 10;
    this.queryList();
  }
}