import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { Router } from '@angular/router';

import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';
import { InterestDiscountTableBean, InterestDiscountTableDetailBean } from '../bean/middle-interest-discount.bean';


@Component({
    selector: "interest-discount-table",
    templateUrl: './interest-discount-table.component.html',
    styleUrls: ['./interest-discount-table.component.scss'],
})

export class InterestDiscountTable implements OnInit {
    code: any;
    first: any = 1;
    total: any;
    dataTable: any[];
    msgs: any;
    ch: any = CALENDAR_CN;
    likeFlag: any = [];
    bean: InterestDiscountTableBean = new InterestDiscountTableBean();
    detailBean: InterestDiscountTableDetailBean = new InterestDiscountTableDetailBean();
    tableHead: any[] = [
        {
            label: '核心企业客户号',
            value: 'idOfCoreByBank',
            wth: '150px'
        }, {
            label: '核心企业名称',
            value: 'custName',
            wth: '150px'
        }, {
            label: '核心企业ID号',
            value: 'platformCustId',
            wth: '150px'
        }, {
            label: '合同编号',
            value: 'contractNumber',
            wth: '150px'
        }];
    beginTradeDate: Date = null;
    endTradeDate: Date = null;
    loandis: boolean = false;
    addValue: any = [];
    constructor(
        public router: Router,
        public http: ProjectHttpService,
        public commfunc: Commfunc,
    ) {
        this.codeValues();
    }

    ngOnInit() {
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.queryList()
    }

    query() {
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.queryList()
    }

    queryList() {
        this.msgs = [];
        //增加模糊查询
        // if (this.likeFlag > 0) {
            this.bean.likeFlag = 1;
        // } else {
        //     this.bean.likeFlag = 0;
        // }
        if (this.beginTradeDate && typeof this.beginTradeDate != 'string') {
            this.bean.beginTradeDate = this.commfunc.transDateN(this.beginTradeDate);
        }
        if (this.endTradeDate && typeof this.endTradeDate != 'string') {
            this.bean.endTradeDate = this.commfunc.transDateN(this.endTradeDate);
        }
        this.http.supplyChainRepayQueryList(this.bean).subscribe(data => {
            if (data['returnCode']['code'] == 'success') {
                this.dataTable = data['returnList'];
                this.total = data['totalNum'];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            }
            else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }

    clear() {
        this.bean = new InterestDiscountTableBean();
        this.beginTradeDate = null;
        this.endTradeDate = null;
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.queryList()
    }
    // 详情
    detail(e) {
        this.msgs = [];
        if (e.contractNumber == '' || e.contractNumber == null || e.contractNumber == undefined) {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '查询详情条件--合同编号没有' });
            return;
        } else if (this.beginTradeDate == null || this.beginTradeDate == undefined) {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '查询详情条件--账户交易起始日期未填入' });
            return;
        } else if (this.endTradeDate == null || this.endTradeDate == undefined) {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '查询详情条件--账户交易终止日期未填入' });
            return;
        } else {
            let a = Date.parse(this.commfunc.transDateN(this.beginTradeDate));
            let b = Date.parse(this.commfunc.transDateN(this.endTradeDate));
            let c = (b - a) / 86400000;
            if (c > 92) {
                this.msgs.push({ severity: 'error', summary: '提示', detail: '查询详情条件--交易日期不得超过三个月' });
                return;
            } else {
                this.loandis = true;
                this.addValue = {
                    beginTradeDate: this.commfunc.transDateN(this.beginTradeDate),
                    endTradeDate: this.commfunc.transDateN(this.endTradeDate),
                    contractNumber: e.contractNumber,
                    idOfCoreByBank: e.idOfCoreByBank,
                    custName: e.custName,
                    platformCustId: e.platformCustId,
                    // corePayInterest: e.corePayInterest,
                    // actualInterest: e.actualInterest,
                    // rightInterest: e.rightInterest,
                    // custPayInterest:e.custPayInterest
                }
            }
        }

    }

    paginate(event) {
        // 分页
        this.bean.pageSize = event.rows * 1;
        //当前页
        let currentPage = event.page + 1;
        this.bean.pageNum = currentPage;
        this.first = event.page * event.rows;
        this.queryList();
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret
    }
}