import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import { Router, ActivatedRoute } from '@angular/router';
import { TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { CoopProjectFeasibilityPlanSetBean, CoopProjectTypeListQueryBean, nextBean } from 'app/pages/tzb/customCenter/project-main-manage/project-main-manage-add/feasibility-query/bean/feasibility-query.bean';
import { TZB_CUS_UPLOAD } from 'app/@uisftech/common/service/http.constant';
import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';

import { supplyChainPlatformList } from '../../three-platform/bean/three-platform.bean';


@Component({
    selector: "new-program",
    templateUrl: './new-program.component.html',
    styleUrls: ['./new-program.component.scss'],
})

export class NewProgram implements OnInit {
    coopName: any;
    mainFlowNo: any;
    platformName: any;
    platformIdentify: string;
    Platformbean: supplyChainPlatformList = new supplyChainPlatformList();
    platformIdentifyList: any;

    recoverkey: string = '001';
    planid: any;
    postId: any;
    Flag: string = '0';
    specifyNextNode: any;
    dataMap: any;
    dealPerShow: boolean;
    fileTure: boolean = false;
    options1: any[] = [];
    coopProjectFeasibilityPlanSetBean: CoopProjectFeasibilityPlanSetBean = new CoopProjectFeasibilityPlanSetBean();
    coopProjectTypeListQueryBean: CoopProjectTypeListQueryBean = new CoopProjectTypeListQueryBean();
    NextBean: nextBean = new nextBean()
    code: any;
    cusid: any;
    temporaryDataId: any;
    msgs: any = [];
    display: boolean;
    type: any;
    UPLOAD: string = TZB_CUS_UPLOAD;//导入
    fileList: any = [];//上传文件
    callMsg: any;
    outVal: any[];
    bizPkFieldMap: any;
    bizType: any;
    planMap: any[] = [];//改的可行性方案123
    user: any;
    userId: string;

    // 测试文件下载块
    wordtype: string = '0';
    word1: any;
    mmp: any;
    ccp: any;

    constructor(
        public router: Router,
        public commfunc: Commfunc,
        public http: ProjectHttpService,
        public httpWork: CustomeHttpService,
        public activatedRoute: ActivatedRoute,
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user.extParam.postId) {
            this.postId = this.user.extParam.postId;
        }
        this.NextBean.taskId = activatedRoute.snapshot.params.taskId;
        this.coopProjectFeasibilityPlanSetBean.processId = activatedRoute.snapshot.params.processId;
        this.NextBean.userId = this.userId;
        this.NextBean.postId = this.postId;
        let t = this.commfunc.getSessionDataCH('programme-details-from-reminder-do');
        t = JSON.parse(t);
        let datmap = JSON.parse(t['dataMap']);
        if (datmap['bizPkFieldMap']) {
            // 判断新增/追回
            this.recoverkey = '666'
            this.coopProjectFeasibilityPlanSetBean.coopProjectTypeId = datmap['bizPkFieldMap']['coopProjectTypeId'];
        } else {
            this.coopProjectFeasibilityPlanSetBean.coopProjectTypeId = datmap['coopProjectTypeId'];
        }
        if (this.activatedRoute.snapshot.params.mainFlowNo) {
            this.mainFlowNo = this.activatedRoute.snapshot.params.mainFlowNo;
        }
    }

    ngOnInit() {
        this.codeValues();
        this.queryIdentifyList();
        let mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));;
        this.cusid = mesg['userId']
        this.options1 = this.code['planType'];
        this.savebyList();
        let t = this.commfunc.getSessionDataCH('programme-details-from-reminder-do');
        t = JSON.parse(t);
        this.dataMap = JSON.parse(t['dataMap']);
        let planMaptt = this.dataMap.planMap;
        if (planMaptt == '{}' || planMaptt == undefined || planMaptt == null) {
        } else {
            this.preset();
            this.Flag = '1';
        }
        this.queryleixing();
    }
    queryleixing() {
        let param = {
            coopProjectTypeId: this.coopProjectFeasibilityPlanSetBean.coopProjectTypeId
        }
        this.http.coopProjectTypeDetailQuery(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.coopName = data['coopProjectTypeName']
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }

    //获取码值
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //初始化回显赋值
    preset() {
        this.coopProjectFeasibilityPlanSetBean.planName = this.dataMap.planMap.planName;
        this.coopProjectFeasibilityPlanSetBean.custName = this.dataMap.planMap.custName;
        this.coopProjectFeasibilityPlanSetBean.custId = this.dataMap.planMap.custId;
        this.coopProjectFeasibilityPlanSetBean.planType = this.dataMap.planMap.planType;
        this.coopProjectFeasibilityPlanSetBean.planContent = this.dataMap.planMap.planContent;
        this.coopProjectFeasibilityPlanSetBean.planId = this.dataMap.planMap.planId;
        this.coopProjectFeasibilityPlanSetBean.platformIdentify = this.dataMap.platformMap.platformIdentify;
        // 测试文件下载块
        if (this.coopProjectFeasibilityPlanSetBean.planType == '1') {
            let arr = this.coopProjectFeasibilityPlanSetBean.planContent.split('/');
            this.fileList = [{
                fileName: arr[arr.length - 1]
            }];
            this.wordtype = '3';
        } else {
            this.wordtype = '2';
        }
        this.word1 = this.dataMap.planMap.planContent;
    }
    //上传文件
    myUploader(event) {
        console.log(event)
        if (event.xhr.response && event.xhr.status == 200 && event.xhr.statusText == "OK") { //上传成功 后台返回的 文件路径 和文件名
            // this.coopProjectFeasibilityPlanSetBean.planContent = JSON.parse(event.xhr.response);
            this.fileList = JSON.parse(event.xhr.response);
            if (this.fileList.errorMsg) {//后台校验文件名称,抛错
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: this.fileList.errorMsg });
                return
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: '上传成功！' });
            }
        }
        if (this.fileList.length == 1) {
            this.coopProjectFeasibilityPlanSetBean.planContent = this.fileList[0]['fileUrl'] + this.fileList[0]['fileName'];
            // 测试文件下载块
            this.mmp = this.coopProjectFeasibilityPlanSetBean.planContent;
            // 测试文件下载块

        }

    }
    //上传文件展示删除
    deleta21() {
        this.fileList = [];
        // 测试文件下载块
        this.mmp = '';
        this.coopProjectFeasibilityPlanSetBean.planContent = '';
        this.fileTure = false;
    }
    // onSelect(e){
    //     if(e.files.length!=1){
    //         e.files=[];
    //         this.msgs = [];
    //         this.msgs.push({ severity: 'error', summary: '提示', detail: '只能上传一个文件!' });  
    //     }
    //     console.log(e);
    // }

    // 测试文件下载块
    ddt() {
        this.msgs = [];
        if (this.coopProjectFeasibilityPlanSetBean.planType == '0' && this.coopProjectFeasibilityPlanSetBean.planContent.length > 250) {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '超出限制字数250,可以选择文件上传' });
            return
        }
        this.ccp = this.coopProjectFeasibilityPlanSetBean.planContent;
    }
    // 测试文件下载块
    typedele() {
        // 回显为文件
        if (this.wordtype == '3') {
            if (this.coopProjectFeasibilityPlanSetBean.planType == '0') {
                this.coopProjectFeasibilityPlanSetBean.planContent = '';
                this.coopProjectFeasibilityPlanSetBean.planContent = this.ccp;
            } else if (this.coopProjectFeasibilityPlanSetBean.planType == '1') {
                this.coopProjectFeasibilityPlanSetBean.planContent = '';
                this.fileList = [];
                // this.coopProjectFeasibilityPlanSetBean.planContent = this.mmp;
            }
        }
        // 回显为文本
        else if (this.wordtype == '2') {
            if (this.coopProjectFeasibilityPlanSetBean.planType == '0') {
                this.coopProjectFeasibilityPlanSetBean.planContent = '';
            } else if (this.coopProjectFeasibilityPlanSetBean.planType == '1') {
                this.coopProjectFeasibilityPlanSetBean.planContent = '';
                this.coopProjectFeasibilityPlanSetBean.planContent = this.mmp;
            }
        }
        // 初始
        else if (this.wordtype == '0') {
            if (this.coopProjectFeasibilityPlanSetBean.planType == '0') {
                this.coopProjectFeasibilityPlanSetBean.planContent = '';
                this.coopProjectFeasibilityPlanSetBean.planContent = this.ccp;
            } else if (this.coopProjectFeasibilityPlanSetBean.planType == '1') {
                this.coopProjectFeasibilityPlanSetBean.planContent = '';
                this.coopProjectFeasibilityPlanSetBean.planContent = this.mmp;
            }
        }
    }

    change() {
        this.msgs = [];
        if (this.coopProjectFeasibilityPlanSetBean.planName.length > 50) {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '可行性方案名称字数超出限制字数50' });
            return;
        }
    }
    before(e) {
        console.log(e)
    }

    perTreeShow() {
        // 文沥
        if (this.platformIdentify || this.coopProjectFeasibilityPlanSetBean.custId != null) {
            this.display = true;
        } else {
            this.display = false;
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择平台' });
        }

        //this.display = true;
    }

    getMsg(e) {
        // 文沥
        this.coopProjectFeasibilityPlanSetBean.custId = e['idOfCoreByBank'];
        // this.coopProjectFeasibilityPlanSetBean.custId = e['custNo'];
        this.coopProjectFeasibilityPlanSetBean.custName = e['custName']
        this.display = false;
    }
    // 查询平台下拉
    queryIdentifyList() {
        this.Platformbean.pageSize = 2000;
        this.Platformbean.pageNum = 1;
        this.Platformbean.status = '1';
        // 删除之前选中的人
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextNode) {
            delete s.extParam.specifyNextNode;
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.http.supplyChainPlatformList(this.Platformbean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.platformIdentifyList = data['returnList'];
                this.platformIdentifyList.forEach(e => {
                    e.label = e.platformName;
                    e.value = e.platformIdentify;
                })
                let first = { label: '请选择' }
                this.platformIdentifyList.unshift(first)
                this.changeplatformName()
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }
    changeplatform() {
        this.coopProjectFeasibilityPlanSetBean.custId = '';
        this.coopProjectFeasibilityPlanSetBean.custName = '';
        this.platformIdentify = this.coopProjectFeasibilityPlanSetBean.platformIdentify;
    }

    //提交
    sumbit() {
        this.msgs = [];
        let keybean = this.coopProjectFeasibilityPlanSetBean;
        if (keybean.planType == '0' && keybean.planContent.length > 250) {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '超出限制字数250,可以选择文件上传' });
            return
        } else if (keybean.platformIdentify == null || keybean.platformIdentify == undefined || keybean.platformIdentify == '') {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择平台' });
            return;
        } else if (keybean.custId == null || keybean.custId == undefined || keybean.custId == '') {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择客户' });
            return;
        } else if (keybean.planName == null || keybean.planName == undefined || keybean.planName == '') {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请填写可行性方案名称' });
            return;
        } else if (keybean.planType == null || keybean.planType == undefined || keybean.planType == '') {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择方案文本类型' });
            return;
        } else if (keybean.planContent == null || keybean.planContent == undefined || keybean.planContent == '') {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请填写方案文本内容' });
            return;
        } else if (keybean.planName.length > 50) {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '可行性方案名称字数超出限制字数50' });
            return;
        } else {
            let ss = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            ss['globalSeqNo'] = this.mainFlowNo;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(ss));
            setTimeout(() => {
                this.nextDom();
            }, 1000);
        }

        if (this.dataMap.planMap == null || this.dataMap.planMap == undefined || this.dataMap.planMap == '{}') {
            keybean.operateType = '1';
        } else {
            keybean.operateType = '2';
        }
        if (keybean.planId != '' && keybean.planId != null && keybean.planId != undefined) {
            keybean.operateType = '2';
        }

    }

    selectPersonOutput(e) {
        this.refer(e)
    }


    // 提交后台录入数据
    refer(e: any) {
        this.http.coopProjectFeasibilityPlanSet(this.coopProjectFeasibilityPlanSetBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (JSON.stringify(data['bizPkFieldMap']) != '{}') {
                    this.bizPkFieldMap = data['bizPkFieldMap']
                    this.bizType = data['bizType']
                    delete data.returnCode
                    this.callMsg = data
                    //this.coopProjectFeasibilityPlanSetBean.planId = data['planId']
                    this.planid = data['planId']
                    this.outValueCall(e)
                } else {
                    delete data.returnCode
                    this.callMsg = data
                }
                this.coopProjectFeasibilityPlanSetBean.planId = this.planid
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }
    //工作流选人
    nextDom() {
        let params = {
            userIdMap: {
                userId: this.NextBean.userId,
                userPost: this.NextBean.postId,
            },
            proxessDesc: '可行性方案创建',
            taskId: this.NextBean.taskId,
            bussinessMap: {
                //dataMap: this.callMsg,
                //bizPkFieldMap: this.callMsg['bizPkFieldMap'],
                //bizType: this.callMsg['bizType']
            }
        }
        this.httpWork.bpmFindNextNodeInfo(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.result.nextOutData &&
                    data.result.nextOutData.length >= 1 &&
                    data.result.nextOutData[0].selectUserList &&
                    data.result.nextOutData[0].selectUserList.length >= 1) {
                    this.dealPerShow = true;
                    this.outVal = data['result']['nextOutData'];
                } else {
                    this.noOut();
                }
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }
    outDisplayCall(e) {
        this.dealPerShow = e
    }

    //选人下一步
    outValueCall(e) {
        let persons = '';
        e.forEach(el => {
            persons += el['tellerId'] + ',';
            this.specifyNextNode = el.outFlowId;
        });
        let params = {
            userId: this.NextBean.userId,
            taskId: this.NextBean.taskId,
            bussinessMap: {
                bizPkFieldMap: this.bizPkFieldMap,
                bizType: this.bizType,
                specifyNextOpers: persons,
                specifyNextNode: this.specifyNextNode,
                planId: this.planid,
                bussinessSave: {
                    query_Indexa: this.coopProjectFeasibilityPlanSetBean.custId,
                    query_Indexb: this.coopProjectFeasibilityPlanSetBean.custName
                },
                platformMap: {
                    platformIdentify: this.coopProjectFeasibilityPlanSetBean.platformIdentify,
                },
                dataMap: {
                    planType: this.coopProjectFeasibilityPlanSetBean.planType,
                    coopProjectTypeId: this.coopProjectFeasibilityPlanSetBean.coopProjectTypeId,
                    custId: this.coopProjectFeasibilityPlanSetBean.custId,
                    custName: this.coopProjectFeasibilityPlanSetBean.custName,
                    operateType: this.coopProjectFeasibilityPlanSetBean.operateType,
                    planContent: this.coopProjectFeasibilityPlanSetBean.planContent,
                    planId: this.planid,
                    planName: this.coopProjectFeasibilityPlanSetBean.planName,
                    planMap: {
                        planId: this.planid,
                        planContent: this.coopProjectFeasibilityPlanSetBean.planContent,
                        planName: this.coopProjectFeasibilityPlanSetBean.planName,
                        planType: this.coopProjectFeasibilityPlanSetBean.planType,
                        custName: this.coopProjectFeasibilityPlanSetBean.custName,
                        custId: this.coopProjectFeasibilityPlanSetBean.custId,
                        coopProjectTypeId: this.coopProjectFeasibilityPlanSetBean.coopProjectTypeId,
                    },
                    platformMap: {
                        platformIdentify: this.coopProjectFeasibilityPlanSetBean.platformIdentify,
                    }
                }
            }
        }
        this.doNext(params)
    }
    doNext(params) {
        this.httpWork.bpmNextTask(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                // window.history.go(-1)
                this.router.navigate(['/pages/tzb/custom/reminder-do/backlog-detail/btn-page/workdesk-page'], { queryParams: { flag: '1', totalNum: '1' } })
                //this.router.navigate(['/pages/tzb/custom/reminder-do/backlog-detail/btn-page/worktask', { scheduleTypeId: "RUM", scheduleName: "供应链项目管理" }], { queryParams: { flag: '1', 'id': '3', type: 'new' } });
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    //不需要审批
    noOut() {
        let params = {
            userId: this.NextBean.userId,
            taskId: this.NextBean.taskId,
            bussinessMap: {
                bizPkFieldMap: this.bizPkFieldMap,
                bizType: this.bizType,
                planId: this.planid,
                bussinessSave: {
                    query_Indexa: this.coopProjectFeasibilityPlanSetBean.custId,
                    query_Indexb: this.coopProjectFeasibilityPlanSetBean.custName
                },
                platformMap: {
                    platformIdentify: this.coopProjectFeasibilityPlanSetBean.platformIdentify,
                },
                dataMap: {
                    planType: this.coopProjectFeasibilityPlanSetBean.planType,
                    coopProjectTypeId: this.coopProjectFeasibilityPlanSetBean.coopProjectTypeId,
                    custId: this.coopProjectFeasibilityPlanSetBean.custId,
                    custName: this.coopProjectFeasibilityPlanSetBean.custName,
                    operateType: this.coopProjectFeasibilityPlanSetBean.operateType,
                    planContent: this.coopProjectFeasibilityPlanSetBean.planContent,
                    planId: this.planid,
                    planName: this.coopProjectFeasibilityPlanSetBean.planName,
                    planMap: {
                        planId: this.planid,
                        planContent: this.coopProjectFeasibilityPlanSetBean.planContent,
                        planName: this.coopProjectFeasibilityPlanSetBean.planName,
                        planType: this.coopProjectFeasibilityPlanSetBean.planType,
                        custName: this.coopProjectFeasibilityPlanSetBean.custName,
                        custId: this.coopProjectFeasibilityPlanSetBean.custId,
                        coopProjectTypeId: this.coopProjectFeasibilityPlanSetBean.coopProjectTypeId,
                    },
                    platformMap: {
                        platformIdentify: this.coopProjectFeasibilityPlanSetBean.platformIdentify,
                    }
                }
            }
        }
        this.doNext(params)
    }

    //查询临时数据列表
    savebyList() {
        let params = {
            functionInteface: 'coopProjectFeasibilityPlanSet',
            operatorId: this.cusid,
            systemIdentificationElement: this.activatedRoute['snapshot']['params']['taskId']
        }
        this.http.queryFunctionTemporaryDataList(params).subscribe(data => {
            if (data['returnCode']['code'] == 'success') {
                let dataList = data['dataList'];
                if (dataList.length == 0) {
                    this.temporaryDataId = '';
                    this.addTy()
                } else {
                    let parm = dataList[0]['temporaryDataId'];
                    this.temporaryDataId = dataList[0]['temporaryDataId']
                    this.queryBy(parm);
                }
            }
        })
    }
    //查询单条数据
    queryBy(parm) {

        let params = {
            temporaryDataId: parm
        }
        this.http.queryFunctionTemporaryData(params).subscribe(data => {
            if (data['returnCode']['code'] == 'success') {
                this.coopProjectFeasibilityPlanSetBean = JSON.parse(data['saveData'])['bean'];
                this.addTy()
                if (this.coopProjectFeasibilityPlanSetBean.planType == '1') {
                    let arr = this.coopProjectFeasibilityPlanSetBean.planContent.split('/');
                    this.fileList = [{
                        fileName: arr[arr.length - 1]
                    }];
                }
            }
        })
    }

    //保存
    saveList() {
        let saveData = {
            bean: this.coopProjectFeasibilityPlanSetBean,
        }
        let params = {
            functionInteface: 'coopProjectFeasibilityPlanSet',
            operatorId: this.cusid,
            systemIdentificationElement: this.activatedRoute['snapshot']['params']['taskId'],
            saveData: JSON.stringify(saveData)
        }
        this.http.saveFunctionTemporaryData(params).subscribe(data => {
            if (data['returnCode']['code'] == 'success') {
                // window.history.go(-1)
                this.router.navigate(['/pages/tzb/custom/reminder-do/backlog-detail/btn-page/workdesk-page'], { queryParams: { flag: '1', totalNum: '1' } })
                //this.router.navigate(['/pages/tzb/custom/reminder-do/backlog-detail/btn-page/worktask', { scheduleTypeId: "RUM", scheduleName: "供应链项目管理" }], { queryParams: { flag: '1', 'id': '3', type: 'new' } });
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    click() {
        let arr = this.coopProjectFeasibilityPlanSetBean.planContent.split('/');
        let path = '';
        for (let j = 1; j < arr.length - 1; j++) {
            path += '/' + arr[j];
        }
        path = path + '/';
        let donwname = arr[arr.length - 1];
        open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(donwname) + "&fileUrl=" + path, '_blank');

    }
    //报文头添加
    addTy() {
        let mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        mesg['extParam']['temporaryDataId'] = this.temporaryDataId;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(mesg));
    }
    // 赋值到平台名称
    changeplatformName() {
        this.platformIdentifyList.forEach(f => {
            if (f.platformIdentify == this.coopProjectFeasibilityPlanSetBean.platformIdentify) {
                this.platformName = f.platformName
            }
        })
    }


}