import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { Router } from '@angular/router';

import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';
import { coopEleAdjustHistoryListQuery } from 'app/pages/tzb/customCenter/project-main-manage/project-main-manage-add/project-protocol-query/change-record/bean/change-record.bean';


@Component({
    selector: "change-record",
    templateUrl: './change-record.component.html',
    styleUrls: ['./change-record.component.scss'],
})

export class ChangeRecord implements OnInit {
    code: any;
    detial: boolean
    first: any = 1;
    total: any;
    dataTable: any[];
    adjustSeqno: any;
    msgs: any;
    bean: coopEleAdjustHistoryListQuery = new coopEleAdjustHistoryListQuery();
    detailModel: any = {
        adjustSeqno: '',
        projectName: ''
    }
    

    constructor(
        public router: Router,
        public http: ProjectHttpService,
        public commfunc: Commfunc,
    ) {
        this.codeValues();
    }



    ngOnInit() {
        this.newAdjustElement.queryAdjustElement();
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.queryList()
    }

    query() {
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.queryList()
    }

    newAdjustElement = {
        parent: this,
        adjustElementList: null,
        queryAdjustElement () {
            let param = { "coopProjectTypeId": 'G10001' };
            this.parent.http.queryAdjustElement(param).subscribe(data => {
                if (data['returnCode']['code'] == 'success') {
                    this.adjustElementList = this.fixAdjustElement(data['elementMap']['adjustList']);
                } else {
                    this.parent.msgs = [];
                    this.parent.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            })
        },
        fixAdjustElement (list) {
            let ret = [];
    
            if (list && list.length) {
                ret.push({ label: '请选择', value: '' });
                list.forEach(element => {
                    let obj = {
                        label: element.adjustValue,
                        value: element.adjustKey
                    }
                    ret.push(obj);
                });
            }
    
            return ret;
        }
    }



    queryList() {
        this.msgs = [];
        this.http.coopEleAdjustHistoryListQuery(this.bean).subscribe(data => {
            //console.log(data);
            if (data['returnCode']['code'] == 'success') {
                this.dataTable = data['coopEleAdjustHistoryList'];
                this.total = data['total'];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            }
            else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }

    clear() {
        this.bean = new coopEleAdjustHistoryListQuery();
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.queryList()
    }

    detail(e) {
        //console.log(e);
        this.detailModel.adjustSeqno = e['adjustSeqno'];
        this.detailModel.projectName = e['projectName'];
        // this.detial = true;
        this.router.navigate(['/pages/tzb/custom-center/project-main-manage/change-detile'], { queryParams: { adjustSeqno: e['adjustSeqno'] } });
    }

    paginate(event) {
        // 分页
        this.bean.pageSize = event.rows;
        let currentPage = event.page + 1;
        this.bean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.bean.pageSize;

        this.queryList();
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret
    }

}