import { Component, OnInit, Output,Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import { ActivatedRoute } from '@angular/router';
import { CoopProjectFeasibilityPlanDetailQueryBean, CoopProjectFeasibilityPlanSetBean } from '../bean/feasibility-query.bean';
import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';
import { CoopProjectTypeListQueryBean } from 'app/pages/tzb/customCenter/project-main-manage/project-main-manage-search-other/bean/project-main-manage-search-other.bean';

@Component({
    selector:"scheme-modification",
    templateUrl:'./scheme-modification.component.html',
    styleUrls:['./scheme-modification.component.scss'],
})

export class SchemeModification implements OnInit{
    coopProjectFeasibilityPlanDetailQueryBean:CoopProjectFeasibilityPlanDetailQueryBean=new CoopProjectFeasibilityPlanDetailQueryBean();
    coopProjectTypeListQueryBean:CoopProjectTypeListQueryBean=new CoopProjectTypeListQueryBean();
    options1:any[]=[];
    textType:any='01';
    msgs:any[];
    typeTitle:any;
    display:boolean=false;
    dialogType:any='01';
    detailsDisplay:boolean=false;
    tableData:any[]=[];
    code:any;
    List:any;
    coopProjectFeasibilityPlanSetBean:CoopProjectFeasibilityPlanSetBean=new CoopProjectFeasibilityPlanSetBean();
    type:any;
    constructor(
        public activatedRoute:ActivatedRoute,
        public http:ProjectHttpService,
        public commfunc:Commfunc,
    ){
        activatedRoute.queryParams.subscribe(queryParams => {
            this.coopProjectFeasibilityPlanDetailQueryBean.planId = queryParams['planId'];
                this.getMsgs();
        })
        
    }
    ngOnInit() {
        this.queryList();
        this.codeValues();
        this.options1 = this.code['planType']
    }
    //获取码值
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    getMsgs() {
        this.http.coopProjectFeasibilityPlanDetailQuery(this.coopProjectFeasibilityPlanDetailQueryBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.coopProjectFeasibilityPlanSetBean=data
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }
    queryList(){
        this.coopProjectTypeListQueryBean.pageSize=20000;
        this.coopProjectTypeListQueryBean.pageNum=1;
        this.http.coopProjectTypeListQuery(this.coopProjectTypeListQueryBean).subscribe(data=>{
            if(data.returnCode.code=='success'){
                this.List=data['returnList'];
                this.List.forEach(e=>{
                    e.label=e.coopProjectTypeName;
                    e.value=e.coopProjectTypeId;
                })
                let first={label:'请选择'}
                this.List.unshift(first)
                console.log(this.List)
            }else{
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        },err=>{
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }
    perTreeShow(){
        this.display=true;
    }
    //提交
    sumbit(){
        this.msgs=[]
        if(this.coopProjectFeasibilityPlanSetBean.custId==null||this.coopProjectFeasibilityPlanSetBean.custId==undefined){
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择客户' });
            return;
        }else if(this.coopProjectFeasibilityPlanSetBean.coopProjectTypeId==null||this.coopProjectFeasibilityPlanSetBean.coopProjectTypeId==undefined){
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请填写合作项目类型编号' });
            return;
        }else if(this.coopProjectFeasibilityPlanSetBean.planType==null||this.coopProjectFeasibilityPlanSetBean.planType==undefined){
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择方案类型' });
            return;
        }else if(this.coopProjectFeasibilityPlanSetBean.planContent==null||this.coopProjectFeasibilityPlanSetBean.planContent==undefined){
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请填写方案内容' });
            return;
        }else if(this.coopProjectFeasibilityPlanSetBean.planName==null||this.coopProjectFeasibilityPlanSetBean.planName==undefined){
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请填写可行性方案名称' });
            return;
        }
        this.coopProjectFeasibilityPlanSetBean.operateType='2';
        this.http.coopProjectFeasibilityPlanSet(this.coopProjectFeasibilityPlanSetBean).subscribe(data=>{
            if(data.returnCode.code=='success'){
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            }else{
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        },err=>{
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }

}