import { inject } from '@angular/core/testing';
import { Injectable, NgModule } from '@angular/core';
import { HttpService } from 'app/@uisftech/common/service/http.service';
import { TZB_HTTP_CUS, TZB_HTTP_CUS1, TZB_HTTP_CUS2, TZB_HTTP_CUS22, TZB_HTTP_CUSz } from 'app/@uisftech/common/service/http.constant';
import { API } from './supply-chain.constant';
@Injectable()
export class SupplyChainHttpService {
    constructor(public httpService: HttpService) {
    }
    //查询第三方企业客户签约信息
    supplyChainCustCodeInfoQuery(params) {
        params = this.httpService.handleParams(params)
        return this.httpService.post(TZB_HTTP_CUSz + API.supplyChainCustCodeInfoQuery, params);
    }
    //查询金链条核心客户信息和客户信息表
    supplyChainOnlineMember(params) {
        params = this.httpService.handleParams(params)
        return this.httpService.post(TZB_HTTP_CUSz + API.supplyChainOnlineMember, params);
    }
    supplyChainCustInfo(params) {
        params = this.httpService.handleParams(params)
        return this.httpService.post(TZB_HTTP_CUSz + API.supplyChainCustInfo, params);
    }
    supplyChainWebOrderList(params) {
        params = this.httpService.handleParams(params)
        return this.httpService.post(TZB_HTTP_CUSz + API.supplyChainWebOrderList, params);
    }
    supplyChainOrderInfo(params) {
        params = this.httpService.handleParams(params)
        return this.httpService.post(TZB_HTTP_CUSz + API.supplyChainOrderInfo, params);
    }
    // 客户删除
    supplyChainDelCustInfo(params) {
        params = this.httpService.handleParams(params)
        return this.httpService.post(TZB_HTTP_CUSz + API.supplyChainDelCustInfo, params);
    }
    // 客户删除历史详情查询
    supplyChainQueryDelCustInfo(params) {
        params = this.httpService.handleParams(params)
        return this.httpService.post(TZB_HTTP_CUSz + API.supplyChainQueryDelCustInfo, params);
    }
}