import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { Router } from '@angular/router';

import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';
import { ContractsTableBean } from '../bean/contracts-loans.bean';
import { checkEnAndNum, checkNumber } from 'app/@uisftech/common/service/validator';


@Component({
    selector: "contracts-table",
    templateUrl: './contracts-table.component.html',
    styleUrls: ['./contracts-table.component.scss'],
})

export class ContractsTable implements OnInit {
    contractStatus: any;//by huj++
    guaranteeMode: any;
    code: any;
    first: any = 1;
    total: any;
    dataTable: any[];
    msgs: any;
    ch: any = CALENDAR_CN;
    likeFlag: any = [];
    bean: ContractsTableBean = new ContractsTableBean();
    tableHead: any[] =
        [
            {
                label: '核心企业客户号',
                value: 'corporationCode',
                wth: '150px'
            },
            {
                label: '核心企业客户名称',
                value: 'corporationName',
                wth: '150px'
            },
            {
                label: '客户号',
                value: 'clientNo',
                wth: '150px'
            }, {
                label: '客户名称',
                value: 'clientName',
                wth: '150px'
            }, {
                label: '合同编号',
                value: 'contractNo',
                wth: '150px'
            }];
    tableHead1: any[] = [{
        label: '录入日期',
        value: 'appDate',
        wth: '150px'
    }, {
        label: '起贷日期',
        value: 'loanDate',
        wth: '150px'
    },
    {
        label: '止贷日期',
        value: 'loanDueDate',
        wth: '150px'
    },
    // {
    //     label: '合同状态',
    //     value: 'contractStatus',
    //     wth: '150px'
    // }
    //    ,
    //     {
    //         label: '业务品种',
    //         value: 'bussType',
    //         wth: '150px'
    //     }, {
    //         label: '子业务品种',
    //         value: 'subBussType',
    //         wth: '150px'
    //     }  by huj
    //, 
    {
        label: '产品名称',
        value: 'productName',
        wth: '150px'
    }
    ];
    beginLoanStartDate: Date = null;
    endLoanStartDate: Date = null;
    beginLoanDueDate: Date = null;
    endLoanDueDate: Date = null;
    loandis: boolean = false;
    constructor(
        public router: Router,
        public http: ProjectHttpService,
        public commfunc: Commfunc,
    ) {
        this.codeValues();
    }

    ngOnInit() {
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.queryList()
        this.guaranteeMode = this.code['guaranteeType1'];
        this.contractStatus = this.code['contractStatus'];
    }

    query() {
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.queryList()
    }

    queryList() {
        this.msgs = [];
        //增加模糊查询
        // if (this.likeFlag > 0) {
        this.bean.likeFlag = 1;
        // } else {
        //     this.bean.likeFlag = 0;
        // }
        if (this.beginLoanStartDate && typeof this.beginLoanStartDate != 'string') {
            this.bean.beginLoanStartDate = this.commfunc.transDateN(this.beginLoanStartDate);
        }
        if (this.endLoanStartDate && typeof this.endLoanStartDate != 'string') {
            this.bean.endLoanStartDate = this.commfunc.transDateN(this.endLoanStartDate);
        }
        if (this.beginLoanDueDate && typeof this.beginLoanDueDate != 'string') {
            this.bean.beginLoanDueDate = this.commfunc.transDateN(this.beginLoanDueDate);
        }
        if (this.endLoanDueDate && typeof this.endLoanDueDate != 'string') {
            this.bean.endLoanDueDate = this.commfunc.transDateN(this.endLoanDueDate);
        }
        this.http.supplyChainContractQueryList(this.bean).subscribe(data => {
            if (data['returnCode']['code'] == 'success') {
                this.dataTable = data['contractList'];
                this.total = data['totalNum'];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            }
            else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }

    clear() {
        this.bean = new ContractsTableBean();
        this.beginLoanStartDate = null;
        this.endLoanStartDate = null;
        this.beginLoanDueDate = null;
        this.endLoanDueDate = null;
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.queryList()
    }
    paginate(event) {
        // 分页
        this.bean.pageSize = event.rows;
        let currentPage = event.page + 1;
        this.bean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.bean.pageSize;
        this.queryList();
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret
    }
    // 非数校验
    onCheck(event) {
        this.msgs = [];
        if (event.target.value != '') {
            if (checkNumber(null, event.target.value)) {
                this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入正确的数字查询值' });
                event.target.value = '';
            }
        }
    }
}