@Component({
    selector: "subject-adjustment-de",
    templateUrl: './subject-adjustment-de.component.html',
    styleUrls: ['./subject-adjustment-de.component.scss'],
})

export class SubjectAdjustmentDe implements OnInit {
    options1: any[] = [];
    methods: any[] = [];
    msgs: any[];
    code: any;
    changeBean: coopProjectElementQuery = new coopProjectElementQuery();
    postBean: coopProjectElementAdjust = new coopProjectElementAdjust();
    method: any = '01';
    projectId: any;
    changeType: any = '01';
    beforeAdjust: any;
    ch: any = CALENDAR_CN;
    //调整前
    before_1: any;
    before_2: any;
    before_3: {} = {
        guarantyStyle: '',
        margin: "",
        marginAccount: "",
        marginScale: "",
        projectLimit:""
    };
    before_4: {} = {
        guarantyStyle: '',
        margin: "",
        marginAccount: "",
        marginScale: "",
        projectLimit:""
    };
    before_5: any;
    before_6: any;
    before_7: any;
    before_8: any;
    //调整后
    afterad_1: any;
    afterad_2: any;
    afterad_3: {} = {
        guarantyStyle: '',
        margin: "",
        marginAccount: "",
        marginScale: "",
        projectLimit:""
    };
    afterad_4: {} = {
        guarantyStyle: '',
        margin: "",
        marginAccount: "",
        marginScale: "",
        projectLimit:""
    };
    afterad_5: any;
    afterad_6: any;
    afterad_7: any;
    afterad_8: Date = null;
    adjustElement: any;
    adjustSeqno: any;
    dataList:any;
    adjustReason:any;
    adjustOpinion:any;


    constructor(
        public router: Router,
        public activatedRoute: ActivatedRoute,
        public http: ProjectHttpService,
        public commfunc: Commfunc,

    ) {console.log(activatedRoute)
        if (activatedRoute['snapshot']['params']['dataMap'] != undefined) {
            this.dataList = JSON.parse(activatedRoute['snapshot']['params']['dataMap'])['bizPkFieldMap'];
            console.log(this.dataList)
            this.projectId = this.dataList['projectId'];
            this.adjustElement = this.dataList['adjustElement'];
            this.adjustSeqno = this.dataList['adjustSeqno'];
            // this.adjustSeqno=this.dataList['adjustSeqno'];
            this.adjustReason=this.dataList['adjustReason'];
            this.adjustOpinion=this.dataList['adjustOpinion'];
            this.matchList();
        }
        // console.log(JSON.parse(activatedRoute['snapshot']['params']['dataMap']))
        // this.projectId=JSON.parse(activatedRoute['snapshot']['params']['dataMap'])['bizPkFieldMap']['projectId'];

        // activatedRoute.queryParams.subscribe(queryParams => {
        //     console.log(queryParams)
        //     this.projectId = queryParams['projectId']
        // })
    }
    ngOnInit() {
        this.codeValues();
        
    }
    // sumbit() {
    //     this.router.navigate(['/pages/tzb/custom-center/project-main-manage/project-main-manage-add'], { queryParams: { state: '3' } })
    // }
    matchList(){
        console.log(this.dataList['beforeAdjust'])
        if (this.adjustElement == '001') {
            this.before_1 = this.dataList['beforeAdjust'].split('^')[1];
            this.afterad_1 = this.dataList['afterAdjust'].split('^')[1];

        } else if (this.adjustElement == '002') {
            this.before_2 = this.dataList['beforeAdjust'].split('^')[1];
            this.afterad_2 = this.dataList['afterAdjust'].split('^')[1];
            console.log(this.before_2);
            console.log(this.afterad_2)

        } else if (this.adjustElement == '003') {
            // "guarantyStyle^1:margin^66666:marginAccount^100271000100030:marginScale^1:3"
            let st = this.dataList['beforeAdjust'];
            let arr = st.split(':');
            for (let i = 0; i < arr.length; i++) {
                let arr_1 = arr[i].split('^');
                for (let key in this.before_3) {
                    if (key == arr_1[0]) {
                        this.before_3[key] = arr_1[1];
                    }
                }
            }
            let st_a = this.dataList['afterAdjust'];
            let arr_a = st_a.split(':');
            for (let i = 0; i < arr_a.length; i++) {
                let arr_1 = arr_a[i].split('^');
                for (let key in this.before_3) {
                    if (key == arr_1[0]) {
                        this.afterad_3[key] = arr_1[1];
                    }
                }
            }
            console.log(this.before_3)
        } else if (this.adjustElement == '004') {
            let st = this.dataList['beforeAdjust'];
            let arr = st.split(':');
            for (let i = 0; i < arr.length; i++) {
                let arr_1 = arr[i].split('^');
                for (let key in this.before_4) {
                    if (key == arr_1[0]) {
                        this.before_4[key] = arr_1[1];
                    }
                }
            }

            let sta = this.dataList['afterAdjust'];
            let arra = sta.split(':');
            for (let i = 0; i < arra.length; i++) {
                let arr_1a = arra[i].split('^');
                for (let key in this.before_4) {
                    if (key == arr_1a[0]) {
                        this.afterad_4[key] = arr_1a[1];
                    }
                }
            }

        } else if (this.adjustElement == '005') {
            this.before_5 = this.dataList['beforeAdjust'].split('^')[1];
            this.afterad_5 = this.dataList['afterAdjust'].split('^')[1];
        } else if (this.adjustElement == '006') {
            this.before_6 = this.dataList['beforeAdjust'].split('^')[1];
            this.afterad_6 = this.dataList['afterAdjust'].split('^')[1];
        } else if (this.adjustElement == '007') {
            this.before_7 = this.dataList['beforeAdjust'].split('^')[1];
            this.afterad_7 = this.dataList['afterAdjust'].split('^')[1];            
        } else if (this.adjustElement == '008') {
            this.before_8 = this.dataList['beforeAdjust'].split('^')[1];
            this.afterad_8 = this.dataList['afterAdjust'].split('^')[1];        }
    }
    

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret
    }
}