import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message, ConfirmationService, ConfirmDialogModule } from 'primeng/primeng';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { Router } from '@angular/router';

import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';
import { SupplyChainPlatformAdd, supplyChainPlatformList, supplyChainPlatformDetail, supplyChainPlatformModify, supplyChainPlatformDelete } from 'app/pages/tzb/customCenter/project-main-manage/task-center/bean/task-report.bean';
import { UisftechCommonHttpService } from '../../../../../../@uisftech/common/http/uisftech-common-http.service';
import { CommonHttpService } from '../../../../common/http/common.http.service';
import { OnlyYearMonthSelectComponent } from '../../../../../../@uisftech/common/directive/monthly-calendar/monthly-calendar/monthly-calendar.component';
import { DatePipe } from '@angular/common';


@Component({
    selector: "month-report",
    templateUrl: './month-report.component.html',
    styleUrls: ['./month-report.component.scss'],
    providers: [Commfunc, ConfirmationService, DatePipe]
})

export class MonthReport implements OnInit {
    ch: any = CALENDAR_CN;
    code: any;
    first: number = 0;
    total: any;
    dataTable: any[];
    msgs: any;
    bean: supplyChainPlatformList = new supplyChainPlatformList();
    deleteBean: supplyChainPlatformDelete = new supplyChainPlatformDelete();
    detailBean: supplyChainPlatformDetail = new supplyChainPlatformDetail();
    user: any;
    startCreateDate: Date = null;
    endTradeDate: Date = null;
    constructor(
        public router: Router,
        public http: ProjectHttpService,
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService,
        public commonHttpService: CommonHttpService,
        public datePipe: DatePipe,
    ) {
        this.codeValues();
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.bean['orgId'] = this.user.extParam.orgId;
        this.bean['taskCategoryId'] = '460000';        
    }

    ngOnInit() {
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.TimeToDate();
    }

    query() {
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.first = 0;        
        this.queryList(this.bean);
    }

    queryList(param) {
        this.msgs = [];
        if (this.commfunc.judgeexpireDate(this.bean['startCreateDate'], this.bean['endCreateDate'])) {
            this.bean['endCreateDate'] = '';
            this.msgs.push({ severity: 'info', summary: '提示', detail: '结束日期不能小于开始日期，请重新输入！' });
            return;
        } else if (this.bean['startCreateDate'] && this.bean['endCreateDate']) {
            let startTime = this.bean['startCreateDate'];
            let endTime = this.bean['endCreateDate'];
            this.bean['startCreateDate'] = this.commfunc.transDateN(startTime);
            this.bean['endCreateDate'] = this.commfunc.transDateN(endTime);
        }
        this.http.queryReportList(param).subscribe(data => {
            if (data['returnCode']['code'] == 'success') {
                this.dataTable = data['resultList'];
                this.total = data['total'];
                console.log(this.dataTable);
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            }
            else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }

    clear() {
        this.bean = new supplyChainPlatformList();
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.first = 0;
        this.TimeToDate();
    }
    // 详情
    detail(e) {
        this.router.navigate(['/pages/tzb/custom-center/project-main-manage/MonthReportWord'], { queryParams: { partyOrGroupNo: e['partyOrGroupNo'], partyOrGroupName: e['partyOrGroupName'], createDate: e['createDate'] } });
    }

    // 机构查询
    orgQueryClick(params) {
        this.commonHttpService.queryOrgByCombConditionsLevel(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.bean['receiveOrgName'] = data.orgInfoList[0].orgName;
                this.queryList(this.bean);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }

    // 默认赋值当月日期
    TimeToDate() {
        // let date = this.commfunc.getNowFormatDate();
        let date = this.commfunc.transDateN(new Date());
        let date2 = date.substr(5, 2);
        let year = date.substr(0, 4);
        let month1 = Number(date2) - 1;
        let day = 0;
        if (month1 == 1 || month1 == 3 || month1 == 5 || month1 == 7 || month1 == 8 || month1 == 10 || month1 == 12) {
            day = 31;
        } else if (month1 == 4 || month1 == 6 || month1 == 8 || month1 == 9 || month1 == 11) {
            day = 30;
        } else if (month1 == 2) {
            if (Number(year) % 4 == 0) {
                day = 29;
            } else {
                day = 28;
            }
        }
        let month = month1.toString();
        if (month.length == 1) {
            month = 0 + month;
        }
        console.log(year + '-' + month + '-' + 1)
        this.bean['startCreateDate'] = this.datePipe.transform(year + '-' + month + '-' + '01', 'yyyy-MM-dd');
        this.bean['endCreateDate'] = this.datePipe.transform(year + '-' + month + '-' + day, 'yyyy-MM-dd');
        this.bean['orgId'] = this.user.extParam.orgId;
        this.orgQueryClick(this.bean);
    }

    paginate(event) {
        // 分页
        this.bean.pageSize = Number(event.rows);
        let currentPage = event.page + 1;
        this.bean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.bean.pageSize;
        this.queryList(this.bean);
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret
    }

}