import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { TZB_HTTP_XIN } from 'app/@uisftech/common/service/http.constant';
import { Router } from '@angular/router';
import { CustInfoTabBean } from './bean/custInfoTab.bean';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { SupplyChainHttpService } from '../http/supply-chain.http.service';
import { CusViewHttpService } from '../../../../../custom/http/custom-view/custom-view.http.service';
import { checkNumber, checkEnAndNum } from '../../../../../../../@uisftech/common/service/validator';
import { ProjectHttpService } from '../../../http/project-main-manage.service';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
@Component({
    selector: 'custom-info-table',
    templateUrl: './custom-info-table.component.html',
    styleUrls: ['./custom-info-table.component.scss'],
    providers: [Commfunc, SupplyChainHttpService, CusViewHttpService]
})
/**客户信息表 */
export class CustomInfoTableComponent implements OnInit {
    platformName: any;
    custId: any;
    projectId: any;
    outVal: any;
    dealPerShow: boolean;
    platformIdentifyList: any;
    msgs: Message[] = []; //提示信息
    custInfoTabBean: CustInfoTabBean = new CustInfoTabBean()
    dataList: any = [];
    code: any;
    SignStatus: any = [];//签约状态
    ch: any = CALENDAR_CN;
    CorporationCode: any = [];
    first: number = 0;//分页控制
    totalNum: number;
    signDate: Date = null;
    recommendDate: Date = null;
    IdType: any = [];//证件类型
    detailPage: boolean = false;
    showFlge: string;
    detailHeader: string;
    inValue: any = {};
    constructor(
        public projectHttpService: SupplyChainHttpService,
        public commfunc: Commfunc,
        public httpService: CusViewHttpService,
        public http: ProjectHttpService,
        public httpWork: CustomeHttpService,
        public router: Router,

    ) {
        this.codeValues() //调用方法，获取全部码值
        this.IdType = this.code['ALL_IDEN_TYPE']; //证件类型

    }

    ngOnInit() {
        this.custInfoTabBean.pageNum = 1;
        this.custInfoTabBean.pageSize = 10;
        this.first = 0;
        this.queryClick();
        // 查询平台识别码列表
        //this.queryIdentifyList();
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //查询按钮
    queryClick() {
        this.custInfoTabBean.pageNum = 1;
        this.custInfoTabBean.pageSize = 10;
        this.first = 0;
        this.query();
    }
    //查询
    query() {
        this.projectHttpService.supplyChainOnlineMember(this.custInfoTabBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.totalNum = data.totalNum;
                this.dataList = data.infoList;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
    }
    //重置
    resetClick() {
        this.custInfoTabBean = new CustInfoTabBean();
        this.querySecleReset();
    }
    //页码重置
    querySecleReset() {
        this.custInfoTabBean.pageNum = 1;
        this.custInfoTabBean.pageSize = 10;
        this.first = 0;
    }

    paginate　(event) {
        //每页显示的条数
        this.custInfoTabBean.pageSize = Number(event.rows);
        //当前页
        let currentPage = event.page + 1;
        this.custInfoTabBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.custInfoTabBean.pageSize;
        this.query();
    }

    //校验客户号只能输入数字
    onKeyupNumber(event, num, item) {
        if (num == '1') {
            if (checkNumber(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '核心企业客户号错误!' }];
                event.target.value = '';
                return
            }
        } else if (num == '2') {
            if (checkNumber(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '客户号错误!' }];
                event.target.value = '';
                return
            }
            if (item && item != '') {
                let param = {
                    custNo: item
                }
                this.httpService.selectCustBriefInfo(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.custInfoTabBean.custName = data.custName;
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
                });
            }
        }
    }
    onKeyupNumId(event) {
        // if (checkEnAndNum(null, event.target.value)) {
        //     this.msgs = [{ severity: 'error', summary: '提示', detail: '核心企业ID号错误!不能输入汉字' }];
        //     event.target.value = '';
        //     return
        // }

    }
    //查看详情
    toDetail(data) {
        this.showFlge = '1';
        this.detailHeader = '详情'
        this.detailPage = true;
        this.inValue = data;
    }

    // 查询平台下拉
    queryIdentifyList() {
        this.custInfoTabBean.pageSize = 2000;
        this.custInfoTabBean.pageNum = 1;
        this.custInfoTabBean.status = '1';
        // 删除之前选中的人
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextNode) {
            delete s.extParam.specifyNextNode;
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.http.supplyChainPlatformList(this.custInfoTabBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.platformIdentifyList = data['returnList'];
                this.platformIdentifyList.forEach(t => {
                    t.label = t.platformIdentify;
                    t.value = t.platformIdentify;
                })
                let first = { label: '请选择' }
                this.platformIdentifyList.unshift(first);
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }

    // 赋值到平台名称
    changeplatform() {
        this.platformIdentifyList.forEach(f => {
            if (f.platformIdentify == this.custInfoTabBean.platformIdentify) {
                this.custInfoTabBean.platformName = f.platformName
            }
        })
    }

    // 删除
    delete(i) {
        this.nextDom();
        this.projectId = i.projectId;
        this.custId = i.clientNo;
        this.platformName = i.platformName
    }

    // 提交后台录入数据
    refer() {
        let param = {
            projectId: this.projectId,
            custId: this.custId,
            platformName: this.platformName
        }
        this.projectHttpService.supplyChainDelCustInfo(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }

            let c = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (c.extParam.specifyNextOpers || c.extParam.specifyNextNode) {
                delete c.extParam.specifyNextOpers;
                delete c.extParam.specifyNextNode;
                this.commfunc.setSessionData('commonHeader', JSON.stringify(c));
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
    }

    outDisplayCall(e) {
        this.dealPerShow = e;
    }

    // 审批选人
    nextDom() {
        this.msgs = [];
        let params = {
            userIdMap: {
                userId: JSON.parse(this.commfunc.getSessionData('commonHeader')).userId,
                userPost: JSON.parse(this.commfunc.getSessionData('commonHeader')).extParam.postId,
            },
            proxessDesc: '选人策略',
            processCode: 'GYLKHSC',
            bussinessMap: {

            }
        }
        this.httpWork.bpmFindNextNodeInfo(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.result.nextOutData && data.result.nextOutData.length >= 1 && data.result.nextOutData[0].selectUserList && data.result.nextOutData[0].selectUserList.length >= 1) {
                    this.dealPerShow = true;
                    this.outVal = data['result']['nextOutData'];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                } else {
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '所要选择人员没有配置' });
                }
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }
    //选人下一步
    outValueCall(e) {
        let persons = '';
        let specifyNextNode = '';
        e.forEach(el => {
            persons += el['tellerId'] + ',';
            specifyNextNode = el.outFlowId;
        });
        let c = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        c.extParam['specifyNextOpers'] = persons;
        c.extParam['specifyNextNode'] = specifyNextNode;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(c));

        this.refer();
    }
}
