import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { Router } from '@angular/router';
import { coopProjectListQueryBean, coopProjectSet } from './bean/project-protocol-query.bean';
import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';


@Component({
    selector: "project-protocol-query-qita",
    templateUrl: './project-protocol-query-qita.component.html',
    styleUrls: ['./project-protocol-query-qita.component.scss'],
})

export class ProjectProtocolQueryQita implements OnInit {
    options1: any[] = [];
    bean: coopProjectListQueryBean = new coopProjectListQueryBean();
    deletBean: coopProjectSet = new coopProjectSet();
    total: any;
    first: any = 0;
    state: any = '1';
    startDate: Date = null;
    endDate: Date = null;
    ch: any = CALENDAR_CN;
    msgs: any;
    typeTitle: any;
    display: boolean = false;
    dialogHead: any = '新增';
    dialogType: any = '01';
    detailsDisplay: boolean = false;
    tableData: any[] = [];
    type: any;
    tableHead: any[] = [
        {
            label: '合作项目协议编号',
            value: 'projectId',
            wth:'150px'
        },
        {
            label: '合作项目名称',
            value: 'projectName',
            wth:'150px'
        },
        {
            label: '大额分期客户群号',
            value: 'crowdId',
            wth:'150px'
        },
        {
            label: '项目协议额度(万元)',
            value: 'projectLimit',
            wth:'165px'
        },
        {
            label: '合作项目客户号',
            value: 'custId',
            wth:'150px'
        },
        {
            label: '合作项目客户名称',
            value: 'custName',
            wth:'150px'
        },
        {
            label: '项目起始日期',
            value: 'beginDate',
            wth:'150px'
        },
        {
            label: '项目结束日期',
            value: 'endDate',
            wth:'150px'
        },
        {
            label: '合作项目状态',
            value: 'projectStatus',
            wth:'165px'
        },
    ];
    //码值
    code: any;
    coopMode: any[];
    projectStatus: any[];
    constructor(
        public router: Router,
        public http: ProjectHttpService,
        public commfunc: Commfunc,
    ) {

    }
    ngOnInit() {
        this.codeValues();
        this.bean.pageNum = 1;
        this.bean.pageSize = 10;
        this.queryList();
        this.coopMode = this.code['coopMode'];
        this.projectStatus = this.code['project_status'];


    }
    copy(item) {
        console.log(item)
        this.router.navigate(['/pages/tzb/custom-center/project-main-manage/subject-details-qita'], { queryParams: { projectId: item['projectId'] } })
    }
    update(item) {
        console.log(item)
        // if (item['projectStatus'] == '0') {
        this.router.navigate(['/pages/tzb/custom-center/project-main-manage/subject-adjustment'], { queryParams: { 
            projectId: item['projectId'],
            projectName: item['projectName'],
            custId: item['custId'],
            custName: item['custName'],
            lei:999
        } })
        // }
        // this.router.navigate(['/pages/tzb/custom-center/project-main-manage/cooperative-roject'],{queryParams:{projectId:item['projectId'],coopProjectTypeId:item['coopProjectTypeId']}})

        // this.router.navigate(['/pages/tzb/custom-center/project-main-manage/cooperative-roject'],{queryParams:{projectId:item['projectId'],coopProjectTypeId:item['coopProjectTypeId']}})

        // if (this.state == '1') {
        //     this.router.navigate(['/pages/tzb/custom-center/project-main-manage/project-theme'])
        // } else {
        //     this.router.navigate(['/pages/tzb/custom-center/project-main-manage/subject-adjustment'])
        // }

    }
    query(){
        this.bean.beginDate = this.commfunc.transDateN(this.startDate);
        this.bean.endDate = this.commfunc.transDateN(this.endDate);
        this.bean.pageNum = 1;
        this.bean.pageSize = 10;
        this.queryList()
    }
    clear() {
        this.bean = new coopProjectListQueryBean();
        this.bean.pageNum = 1;
        this.bean.pageSize = 10;
        this.startDate = null;
        this.endDate = null;
        this.queryList();
    }
    queryList() {
        this.msgs = [];
        this.http.coopProjectListQuery(this.bean).subscribe(data => {
            if (data['returnCode']['code'] == "success") {
                this.tableData = data['returnList'];
                this.total = data['total']
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });

            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }
    delete(i) {
        this.deletBean.operateType = '3';
        this.deletBean.projectId = i['projectId'];
        this.http.coopProjectSet(this.deletBean).subscribe(data => {
            if (data['returnCode']['code'] == "success") {
                this.queryList();
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })

    }
    sumbit(i) {
        this.deletBean.operateType = '4';
        this.deletBean.projectId = i['projectId'];
        this.http.coopProjectSet(this.deletBean).subscribe(data => {
            if (data['returnCode']['code'] == "success") {
                this.queryList();
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    //开始日期
    showDataStart() {
        if (this.startDate && this.endDate) {
            if (this.startDate > this.endDate) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
                setTimeout(() => {
                    this.startDate = null;
                })
            }
        }
    }
    //结束日期
    showDataEnd() {
        if (this.startDate && this.endDate) {
            if (this.startDate > this.endDate) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
                setTimeout(() => {
                    this.endDate = null;
                })
            }
        }
    }
    //手动输入日期时，失去焦点时进行判断
    onchange1() {
        this.showDataStart()
    }
    onchange2() {
        this.showDataEnd()
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret
    }
   
    //分页
    paginate(event) {
        this.bean.pageSize = event.rows * 1;
        //当前页
        let currentPage = event.page + 1;
        this.bean.pageNum = currentPage;
        this.first = event.page * event.rows;
        /**
         * --2018.1.15
         */
        //调用查询的方法
        this.queryList();
    }

}