import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message, ConfirmationService, ConfirmDialogModule } from 'primeng/primeng';
import { ActivatedRoute } from '@angular/router';
import { CoopProjectTypeListQueryBean, CoopProjectTypeSetBean } from './project-creation/bean/project-creation.bean';
import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';
import * as _ from 'lodash';

@Component({
    selector: "project-main-manage-add",
    templateUrl: './project-main-manage-add.component.html',
    styleUrls: ['./project-main-manage-add.component.scss'],
    providers: [Commfunc, ConfirmationService]
})

export class ProjectMainManageAdd implements OnInit {
    menu: any = 0;
    creat_user: any;

    index: any;
    msgs: any[];
    List: any[];
    //  @Input('ind') ind:any;
    coopProjectTypeListQueryBean: CoopProjectTypeListQueryBean = new CoopProjectTypeListQueryBean();

    coopProjectTypeSetBean: CoopProjectTypeSetBean = new CoopProjectTypeSetBean();

    constructor(
        private activatedRoute: ActivatedRoute,
        public http: ProjectHttpService,

        public commfunc: Commfunc,

        public confirmationService: ConfirmationService,

    ) {
        activatedRoute.queryParams.subscribe(queryParams => {
            console.log(queryParams)
            if (queryParams.index) {
                this.index = _.clone(queryParams.index);
            } else {
                this.index = _.clone(this.index);
            }

        })
    }
    ngOnInit() {
        //this.queryList();
        let url = location.href;  //获取url中？后面的参数
        let strs = url.split("/");
        for (let i = 0; i < strs.length; i++) {
            //goback状态
            if (strs[i].indexOf("?") != -1) {
                let arr = strs[i].split("?")
                for (let f = 0; f < strs.length; f++) {
                    if (arr[0] == 'project-main-manage-add') {
                        this.menu = '1'
                    } else if (arr[0] == 'project-main-manage-add2') {
                        this.menu = '2'
                    } else if (arr[0] == 'project-main-manage-add3') {
                        this.menu = '3'
                    } else if (arr[0] == 'project-main-manage-add4') {
                        this.menu = '4'
                    }
                }
            }
            //资源链接状态
            else {
                if (strs[i] == 'project-main-manage-add') {
                    this.menu = '1'
                } else if (strs[i] == 'project-main-manage-add2') {
                    this.menu = '2'
                } else if (strs[i] == 'project-main-manage-add3') {
                    this.menu = '3'
                } else if (strs[i] == 'project-main-manage-add4') {
                    this.menu = '4'
                }
            }

        }
    }
    //创建判断按钮
    buildBtn() {
        this.confirmationService.confirm({
            message: '将发起供应链协议创建流程，是否继续?',
            accept: () => {
                this.build();
            }
        });
    }



    queryList() {
        this.coopProjectTypeListQueryBean.pageSize = 20000;
        this.coopProjectTypeListQueryBean.pageNum = 1;
        this.http.coopProjectTypeListQuery(this.coopProjectTypeListQueryBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.List = data['returnList'];
                this.List.forEach(e => {
                    e.label = e.coopProjectTypeName;
                    e.value = e.coopProjectTypeId;
                })
                let first = { label: '请选择' }
                this.List.unshift(first)

            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }
    handleChange(e) {
        this.index = e.index
    }

    build() {
        this.creat_user = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
        this.msgs = [];

        this.coopProjectTypeSetBean.coopProjectTypeId = 'G10001';
        // 删除之前选中的人
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextNode) {

            delete s.extParam.specifyNextNode;
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        if (this.coopProjectTypeSetBean.coopProjectTypeId != null || this.coopProjectTypeSetBean.coopProjectTypeId != undefined) {
            this.coopProjectTypeSetBean.operateType = '4';
            this.http.coopProjectTypeSet(this.coopProjectTypeSetBean).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                    let pamarm = {
                        processId: data.processId,
                        userId: this.creat_user,
                        startuserId: this.creat_user,
                        curPage: 1,
                        pageRows: 20,
                    };
                    this.http.bpmQueryUnDoTaskList(pamarm).subscribe(data => {
                        if (data.returnCode.code == 'success') {
                            console.log(data);
                            let parem = data.pageData.result[0];

                            this.commfunc.toDisposeSchedule(parem);
                        } else {
                            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                        }
                    }, err => {
                        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
                    })
                } else {
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
            })
        }
        // else {
        //     this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择' });
        // }
    }

    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }

}