import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message, ConfirmationService, ConfirmDialogModule } from 'primeng/primeng';
import { ActivatedRoute } from '@angular/router';
import { CoopProjectTypeListQueryBean, CoopProjectTypeSetBean } from './project-creation/bean/project-creation.bean';
import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';
import * as _ from 'lodash';

@Component({
    selector: "project-main-manage-addX",
    templateUrl: './project-main-manage-addX.component.html',
    styleUrls: ['./project-main-manage-add.component.scss'],
    providers: [Commfunc, ConfirmationService]
})

export class ProjectMainManageAddX implements OnInit {
    creat_user: any;
    msgs: any[];

    coopProjectTypeListQueryBean: CoopProjectTypeListQueryBean = new CoopProjectTypeListQueryBean();

    coopProjectTypeSetBean: CoopProjectTypeSetBean = new CoopProjectTypeSetBean();

    constructor(
        private activatedRoute: ActivatedRoute,
        public http: ProjectHttpService,

        public commfunc: Commfunc,

        public confirmationService: ConfirmationService,

    ) {
    }
    ngOnInit() {
        this.buildBtn();
    }
    //创建判断按钮
    buildBtn() {
        this.confirmationService.confirm({
            message: '将发起供应链协议创建流程，是否继续?',
            accept: () => {
                this.build();
            },
            reject: () => {
                window.history.go(-1);
            }
        });
    }

    build() {
        this.creat_user = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
        this.msgs = [];

        this.coopProjectTypeSetBean.coopProjectTypeId = 'G10001';
        // 删除之前选中的人
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextNode) {

            delete s.extParam.specifyNextNode;
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        if (this.coopProjectTypeSetBean.coopProjectTypeId != null || this.coopProjectTypeSetBean.coopProjectTypeId != undefined) {
            this.coopProjectTypeSetBean.operateType = '4';
            this.http.coopProjectTypeSet(this.coopProjectTypeSetBean).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                    let pamarm = {
                        processId: data.processId,
                        userId: this.creat_user,
                        startuserId: this.creat_user,
                        curPage: 1,
                        pageRows: 20,
                    };
                    this.http.bpmQueryUnDoTaskList(pamarm).subscribe(data => {
                        if (data.returnCode.code == 'success') {
                            console.log(data);
                            let parem = data.pageData.result[0];

                            this.commfunc.toDisposeSchedule(parem);
                        } else {
                            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                        }
                    }, err => {
                        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
                    })
                } else {
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
            })
        }
    }

    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }

}